import React from 'react';
import Lodash from 'lodash';

// UI IMPORTS
import ProductThumbnail from '../sharedComps/ProductThumbnail.js';

function ProductGrid(props) {
    if(props.products !== (null || undefined) && props.products.length !== 0) {
        // takes in an array of product objects
        // maps the list to the productThumbnail component
        // returns the mapped list
        const chunkedSubArrSize = 3;
        let listCopy = [...props.products];
        let chunkedProducts = Lodash.chunk(listCopy,chunkedSubArrSize); // lodash allows for an array of product objects to be section in a 2D array with 3 products per sub-element

        const mappedProducts = chunkedProducts.map((parent)=>
            // mapping parent to build a row
            <div className="row mb-3">
            {parent.map((product, index)=>
                <ProductThumbnail
                    containerSize={index === 2 ? 'wide' : ''}
                    key={product.name + index.toString()}
                    price={product.price}
                    imageLink={product.imageURL}
                    productCategory={product.category}
                    productName={product.name}
                    productLink={"/products/"+product.id}
                />
            )}
            </div>
        );
        return (
            <div> { mappedProducts } </div>
        )
    } else {
        return (
            <h2 className="text-center my-5">Sorry, but it appears these products are unavailable :(</h2>)
    }
}
               
export default ProductGrid;