import React from 'react';
import { connect } from 'react-redux';

// UI COMPONENTS
import Divider from '../sharedComps/Divider.js';
import CustomGrid from './CustomGrid';

class SearchResultsDivider extends React.Component {
    constructor(props) {
        super(props);
        
        this.preSearchMessage = "Begin a search to find your products!"
    }
    
    render() {
        const searchString = this.props.products.searchString;
        return (
            <Divider id={"products"} dividerColor={"light"}>
                {
                    searchString !== null ?
                    <CustomGrid currentCategory={this.props.currentCategory} /> 
                        : 
                    <h2 className="text-center my-5"><b>{this.preSearchMessage}</b></h2>
                }
            </Divider>
        );
    }
}

const mapStateToProps = state => {
    return {
        products: state.searchProducts
    };
}
  
export default connect(
    mapStateToProps
)(SearchResultsDivider);