import actionTypes from './actionTypes';
import * as Server from '../utilityComponents/Server';

export const fetchPageContentByCategory = (categoryString) => async dispatch => {
    // get category image from server
    const catImage = await Server.getCategoryImage(categoryString);

    // shape data
    const payloadValue = { sectionImage: catImage };
    
    dispatch({
        type: actionTypes.FETCH_CONTENT_BY_CATEGORY,
        payload: payloadValue
    });
}