import React from 'react';

function VidThumbnail(props) {
    return (
        <div className="col-sm-12 col-md-12 col-lg-4 col-xl-4 mb-2">
            <iframe allowfullscreen="" frameborder="0" src={props.vidLink} width="100%"></iframe>
        </div>
    );
}

export default VidThumbnail;