import actionTypes from '../actions/actionTypes';

export default (state = {
    searchProducts: [], 
    searchString: null
}, action) => {
    const newState = Object.assign({}, state);
    switch (action.type) {
        case actionTypes.FETCH_PRODUCTS_BY_SEARCH_STRING:
            newState.searchString = action.payload.searchString;
            newState.searchProducts = action.payload.productArr;
            return newState;
        default:
            return state;
    }
};