import actionTypes from './actionTypes';

export const applyPriceAddon = (addonPrice) => async dispatch => {
    // extra price above default product price
    dispatch({
        type: actionTypes.SET_CART_ADDON_PRICE,
        payload: addonPrice
    });
}

export const resetPriceAddon = () => async dispatch => {
    // reset add on price back to zero
    const defaultAddonPrice = 0;
    
    dispatch({
        type: actionTypes.RESET_CART_ADDON_PRICE,
        payload: defaultAddonPrice
    });
} 