import React from 'react';

function Footer(props) {
    return (
        <section id="contact" className="scrollSpySection">
            <div className="container">
                <div className="row">
                    <div className="col-lg-8 mx-auto text-center">
                        <h2 className="section-heading">Contact Us!</h2>
                        <hr className="my-4"/>
                        <p className="mb-5">For Questions, Comments, or Business inquires, email us or visit our social media accounts!</p>
                    </div>
                </div>
                <div className="row">
                    <div className="col-sm-6 col-md-4 col-lg-2 mr-auto text-center ml-auto mb-4"><i className="fa fa-facebook fa-3x mb-3 sr-contact" data-aos="zoom-in" data-aos-duration="300" data-aos-delay="300" data-aos-once="true"></i>
                        <p>Freedom_Inc</p>
                    </div>
                    <div className="col-sm-6 col-md-4 col-lg-2 ml-auto text-center mr-auto mb-4"><i className="fa fa-phone fa-3x mb-3 sr-contact" data-aos="zoom-in" data-aos-duration="300" data-aos-once="true"></i>
                        <p>123-456-6789</p>
                    </div>
                    <div className="col-sm-6 col-md-4 col-lg-3 mr-auto text-center ml-auto mb-4"><i className="fa fa-envelope-o fa-3x mb-3 sr-contact" data-aos="zoom-in" data-aos-duration="300" data-aos-delay="300" data-aos-once="true"></i>
                        <p><a href="mailto:your-email@your-domain.com">email@example.com</a></p>
                    </div>
                    <div className="col-sm-6 col-md-6 col-lg-2 ml-auto text-center mb-4 mr-auto"><i className="fa fa-twitter fa-3x mb-3 sr-contact" data-aos="zoom-in" data-aos-duration="300" data-aos-once="true"></i>
                        <p>Freedom_Inc</p>
                    </div>
                    <div className="col-sm-12 col-md-6 col-lg-2 mr-auto text-center mb-4 ml-auto" id="contact"><i className="fa fa-youtube-play fa-3x mb-3 sr-contact" data-aos="zoom-in" data-aos-duration="300" data-aos-delay="300" data-aos-once="true"></i>
                        <p>Freedom_Inc</p>
                    </div>
                </div>
            </div>
        </section>
    );
}

export default Footer;