import gearImage from '../assets/img/mwClothingRef.jpg';
import clothingImage from '../assets/img/magaStoreRef.jpg'; 
import consumablesImage from '../assets/img/energyDrinkRef.jpg';
import searchImage from '../assets/img/usMilitarySearchPage.jpg';

class DBContent {
    constructor() {
        this.GEAR_CONSTANT = "GEAR";
        this.CLOTHING_CONSTANT = "CLOTHNG";
        this.CONSUMABLES_CONSTANT = "CONSUMABLES";
        this.SEARCH_CONSTANT = "SEARCH";
    }
    
    GetImageByCategory(categoryString) {
        // find and return image based on category
        const upperCasedString = categoryString.toUpperCase();
        switch(upperCasedString) {
            case this.GEAR_CONSTANT:
                return gearImage;
            case this.CLOTHING_CONSTANT:
                return clothingImage;
            case this.CONSUMABLES_CONSTANT:
                return consumablesImage;
            case this.SEARCH_CONSTANT:
                //return searchImage;
                return "https://dslv9ilpbe7p1.cloudfront.net/auU7e7rqnAiKOkyMkOGYFQ_store_banner_image.png";
            default:
                return searchImage;
        }
    }
}

export default DBContent;