import actionTypes from './actionTypes';
import * as Server from '../utilityComponents/Server';

export const fetchProductDataById = (productId) => async dispatch => {
    // get product from server
    const product = await Server.getProductDataById(productId);
    const inventory = await Server.getInventoryById(productId);
    
    dispatch({
        type: actionTypes.FETCH_PRODUCT_DATA_BY_ID,
        payload: {productData: product, inventoryData: inventory}
    });
}

export const resetProductData = () => async dispatch => {
    dispatch({
        type: actionTypes.RESET_PRODUCT_DATA,
        payload: null
    });
}