import React from 'react';
import { withRouter } from 'react-router-dom'
import { Nav, NavItem, NavLink, NavbarBrand } from 'reactstrap';
import { Link } from 'react-router-dom';

// UI IMPORTS
import NavItemContainer from './NavItemContainer.js';
import NavItemDropdown from './NavItemDropdown.js';

// STYLE IMPORTS
import Styles from './Navbar.module.css';
import '../../App.css';

class Navbar extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            location: this.props.location.pathname, // putting location in state ensures that the Navbar re-renders when the path changes
        };
        
        // routes that utilize the home nav
        this.HOME_NAV = ["/home","/"];
        this.LOGIN_NAV = "/login";
        
        this.storeNav = [
            {
                navText: "Mainpage",
                afIcon: "fa-home",
                endPoint: "/store",
                isDropDown: false
            },
            {
                navText: "Search",
                afIcon: "fa-search",
                endPoint: "/search",
                isDropDown: false
            },
            {
                navText: "Cart",
                afIcon: "fa-shopping-cart",
                endPoint: "/cart",
                isDropDown: false
            },
            {
                navText: "Login",
                afIcon: "fa-user",
                endPoint: "/login",
                isDropDown: false
            },
            {
                navText: "Products",
                afIcon: "",
                endPoint: "",
                isDropDown: true,
                dropDownItems: [
                    {
                        navText: "Clothing",
                        endPoint: "/productCategory/clothing",
                    },
                    {
                        navText: "Consumables",
                        endPoint: "/productCategory/consumables",
                    },
                    {
                        navText: "Gear",
                        endPoint: "/productCategory/gear",
                    },
                ]
            },
        ];
        this.storeNavbrandLink = "/home";
        this.homeNav = [
            {
                navText: "About",
                afIcon: "",
                endPoint: "#about",
            },
            {
                navText: "Services",
                afIcon: "",
                endPoint: "#services",
            },
            {
                navText: "Media",
                afIcon: "",
                endPoint: "#media",
            },
            {
                navText: "Sponsors",
                afIcon: "",
                endPoint: "#sponsors",
            },
            {
                navText: "Contact",
                afIcon: "",
                endPoint: "#contact",
            },
            {
                navText: "Store",
                afIcon: "",
                endPoint: "/store",
            },
        ];
        this.homeNavbrandLink = "#page-top";
        
        // content sections
        this.contentSections = null;
        this.navLinkList = null;
        
        // binds
        this.determineNav = this.determineNav.bind(this);
        this.generateAndReturnNavItems = this.generateAndReturnNavItems.bind(this);
        this.assignAnchorTagListeners = this.assignAnchorTagListeners.bind(this);
        this.assignWindowListeners = this.assignWindowListeners.bind(this);
        this.changeNavbarTheme = this.changeNavbarTheme.bind(this);
        this.beginSmoothScroll = this.beginSmoothScroll.bind(this);
        this.isEndpointSame = this.isEndpointSame.bind(this);
        this.isHrefId = this.isHrefId.bind(this);
        this.isHrefPath = this.isHrefPath.bind(this);
        this.parseAndReturnPathName = this.parseAndReturnPathName.bind(this);
        this.parseAndReturnIdName = this.parseAndReturnIdName.bind(this);
        this.debounce = this.debounce.bind(this);
        this.highlightLinkOnScroll = this.highlightLinkOnScroll.bind(this);
        this.findAndSetActiveLink = this.findAndSetActiveLink.bind(this);
        this.removeAllActiveLinks = this.removeAllActiveLinks.bind(this);
        this.collapseNavbar = this.collapseNavbar.bind(this);
        this.assignRefreshToButtons = this.assignRefreshToButtons.bind(this);
        this.determineBrandLink = this.determineBrandLink.bind(this);
    }
    
    componentDidMount() {
        this.assignRefreshToButtons();
        this.assignAnchorTagListeners();
        this.assignWindowListeners();
        this.contentSections = document.querySelectorAll('.scrollSpySection');
        this.navLinkList = document.querySelectorAll('.BSNavLink');
    }
    
    componentDidUpdate() {
        this.assignAnchorTagListeners(); // this syncs the links when a button or external state update occurs
        this.assignRefreshToButtons();
    }
    
    assignAnchorTagListeners() {
        let navLinks = document.getElementsByClassName('js-scroll-trigger');
        for(let i = 0; i < navLinks.length; i++) {
            const currentLink = navLinks[i];
            currentLink.addEventListener('click', this.beginSmoothScroll);
            currentLink.addEventListener('click',this.collapseNavbar)
            
        }
    }
    
    assignWindowListeners() {
        const timeToExecute = 50; //in milliseconds
        window.addEventListener('scroll',this.debounce(this.changeNavbarTheme,timeToExecute));
        window.addEventListener('scroll', this.debounce(this.highlightLinkOnScroll, timeToExecute));
    }
    
    assignRefreshToButtons() {
        // this will force refresh the navbar when a button with a link is clicked
        let buttonList = document.getElementsByClassName('UniversalButton');
        for(let i = 0; i < buttonList.length; i++) {
            buttonList[i].addEventListener('click', this.beginSmoothScroll);
        }
    }
    
    debounce(func, wait, immediate) {
        //debounce function prevents overloaded client
        //function borrowed from https://levelup.gitconnected.com/debounce-in-javascript-improve-your-applications-performance-5b01855e086
        let timeout;
        return function executedFunction() {
            let context = this;
            let args = arguments;
            
            let later = function() {
              timeout = null;
              if (!immediate) func.apply(context, args);
            };
            
            let callNow = immediate && !timeout;
            clearTimeout(timeout);
            timeout = setTimeout(later, wait);
            if (callNow) func.apply(context, args);
        };
    };
    
    collapseNavbar() {
        const navbar = document.getElementsByClassName('navbar-collapse');
        navbar[0].classList.remove('show');
    }
    
    highlightLinkOnScroll() {
        // capture event target
        // query section content
        // check the current position and determine if the content section is in view
        // if section id is equal to an achor tag's href
            // add active class to anchor tag
        const sections = this.contentSections;
        const sectionMargin = 500; // helps trigger the effect earlier (meaing it activates when the section comes into the Viewport on the bottom)
        const currentSectionIndex = sections.length - [...sections].reverse().findIndex((section) => (window.scrollY) >= section.offsetTop - 465) - 1;
        
        console.log("inside scroll event logging sections: ");
        console.log(sections);
        
        let currentSectionEl = null;
        let sectionId = "";
        // check to see if section index is out of array bounds (this is just to prevent null errors)
        if(!(currentSectionIndex >= sections.length) && !(currentSectionIndex < 0)) {
            currentSectionEl = sections[currentSectionIndex];
            sectionId = currentSectionEl.id;
            this.findAndSetActiveLink(sectionId);
        }
        else {
            this.removeAllActiveLinks();
        }
        
        
        console.log('current section id is: ' + sectionId);
        console.log('current index value is; ' + currentSectionIndex);
    }
    
    findAndSetActiveLink(targetSectionId) {
        // loop through navlinks
        // if href is an id
            // if the href of a link is equivalent to the target section id
                // add 'active' to link's classlist
        let links = this.navLinkList;
        for(let i = 0; i < links.length; i++) {
            const currentRawHref = links[i].getAttribute('href');
            if(this.isHrefId(currentRawHref)) {
                let rawId = this.parseAndReturnIdName(currentRawHref);
                let domFriendlyHref = rawId.slice(1,rawId.length);
                
                if(domFriendlyHref == targetSectionId)
                    links[i].classList.add('active');
                else
                    links[i].classList.remove('active');     
            }
        }
    }
    
    removeAllActiveLinks() {
        let links = this.navLinkList;
        for(let i = 0; i < links.length; i++) {
            links[i].classList.remove('active'); 
        }
    }
    
    changeNavbarTheme() {
        let navbar = document.getElementById('mainNav');
        if(document.documentElement.scrollTop > 100)
            navbar.classList.add("navbar-shrink");
        else
            navbar.classList.remove("navbar-shrink");
    }
    
    beginSmoothScroll(e) {
        // stop element from scrolling
        // get target's href value
        
        // if href is id, initiate smooth scroll
        // else if href is not id
            // if location 'pathname' is same as current, do nothing
            // otherwise go to new route
        console.log("smooth scroll fired!");
        const target = e.target;
        const parsedId = this.parseAndReturnIdName(target.getAttribute('href'));
        const parsedPathname = this.parseAndReturnPathName(target.getAttribute('href'));
        if(this.isHrefId(parsedId)) {
            e.preventDefault();
            
            let domFriendlyHref = parsedId.slice(1,parsedId.length);
            const targetScrollElement = document.getElementById(domFriendlyHref);
            const hrefDistanceFromTop = window.pageYOffset + targetScrollElement.getBoundingClientRect().top;
                    
            window.scrollTo({
                top:hrefDistanceFromTop, behavior: 'smooth',
            });
        }
        else if(this.isHrefPath(parsedPathname) && this.isEndpointSame(parsedPathname) !== true){
            this.setState({
                location: parsedPathname,
            });
            console.log("new navbar pathname! " + this.props.location.pathname);
        }
        
    }
    
    isHrefId(expectedId) {
        // if href has '#' char, return true
        for(let i = 0; i < expectedId.length; i++) {
            const currentChar = expectedId[i];
            if(currentChar == '#')
                return true; // is an href
        }
        return false; // not an href
    }
    
    isHrefPath(expectedPath) {
        // if path DOES NOT have a '#' char, return true
        for(let i = 0; i < expectedPath.length; i++) {
            const currentChar = expectedPath[i];
            if(currentChar == '#')
                return false; // not an href
        }
        return true; // is an href
    }
    
    isEndpointSame(expectedPath) {
        const pathName = this.state.location;
        // shallow check
        if(expectedPath[0] === pathName[0]) {
            // deeper check of path names
            const expectedPathName = expectedPath.slice(1,expectedPath.length);
            const currentPathName = pathName.slice(1,pathName.length);
            
            if(expectedPathName === currentPathName)
                return true;
        }
        return false;
    }
    
    parseAndReturnIdName(pathname) {
        // pulls the '/#[id]' out of the pathname string
        // loop through string and returns string of chars after the #
        let returnString = "";
        let startTransfer = false;
        // checking for null to fix random ass error I was getting
        if(pathname !== null) {
            for(let i =0; i < pathname.length; i++) {
                let currentChar = pathname[i];
                if(currentChar == '#')
                    startTransfer = true;
                if(startTransfer)
                    returnString += currentChar;
            }
        }
        return returnString;
    }
    
    parseAndReturnPathName(pathname) {
        // pulls the '/[pathname]' out of the pathname string
        // loop through string and returns string of chars after the #
        let returnString = "";
        let startTransfer = false;
        if(pathname !== null) {
            for(let i =0; i < pathname.length; i++) {
                let currentChar = pathname[i];
                if(currentChar == '/')
                    startTransfer = true;
                if(currentChar == '#')
                    startTransfer = false; // stop string concat if the current char is now on the id
                if(startTransfer)
                    returnString += currentChar;
            }
        }
        return returnString;
    }
    
    determineNav(currentPath) {
        // checks to see if nav pathname matches global constants for certain routes
        // loop only checks if pathname matches the home routes or login (allow for process of elimination, shortening the process)
        let homeMatchFound = false;
        let loginMatchFound = false;
        for(let i = 0; i < this.HOME_NAV.length; i++) {
            if(this.HOME_NAV[i] === currentPath)
                homeMatchFound = true;
            else if(this.LOGIN_NAV === currentPath)
                loginMatchFound = true;
        }
        if(homeMatchFound)
            return this.homeNav; // return homeNav if a matching home route was found
        else if(loginMatchFound)
            return null; // return null since the login page doesn't have a navbar
        else
            return this.storeNav; // return store nav because this is the only other option
    }
    
    determineBrandLink(currentPath) {
        // checks to see if nav pathname matches global constants for certain routes
        // loop only checks if pathname matches the home routes or login (allow for process of elimination, shortening the process)
        let homeMatchFound = false;
        let loginMatchFound = false;
        for(let i = 0; i < this.HOME_NAV.length; i++) {
            if(this.HOME_NAV[i] === currentPath)
                homeMatchFound = true;
            else if(this.LOGIN_NAV === currentPath)
                loginMatchFound = true;
        }
        if(homeMatchFound)
            return this.homeNavbrandLink; // return homeNav if a matching home route was found
        else if(loginMatchFound)
            return null; // return null since the login page doesn't have a navbar
        else
            return this.storeNavbrandLink; // return store nav because this is the only other option
    }
    
    generateAndReturnNavItems(navObj) {
        // takes in a navObj and builds a series of Navitem JSX elements
        let navItems = new Array();
        for(let i = 0; i < navObj.length; i++) {
            const currentNavItem = navObj[i];
            if(currentNavItem.isDropDown !== true) {
                navItems.push(
                    <NavItemContainer
                        key={i.toString()}
                        endPoint={currentNavItem.endPoint}
                        icon={currentNavItem.afIcon}
                        navText={currentNavItem.navText}
                    /> 
                );
            }
            else {
                navItems.push(
                    <NavItemDropdown
                        dropdownObj={currentNavItem}
                    />
                );
            }
            
        }
        return navItems;
    }
    
    render() {
        const NavBarContainer = (props) => {
            if(props.navItems !== null)
                return (
                    <nav className="navbar navbar-light navbar-expand-lg fixed-top" id="mainNav">
                        <div className="container">
                            <NavbarBrand className="js-scroll-trigger" href={props.navbarBrandLink}>Freedom inc</NavbarBrand>
                            <button data-toggle="collapse" data-target="#navbarResponsive" className="navbar-toggler navbar-toggler-right" type="button" aria-controls="navbarResponsive" aria-expanded="false"
                                        aria-label="Toggle navigation">
                                <i className="fa fa-align-justify"></i> 
                            </button>
                            <div className="collapse navbar-collapse" id="navbarResponsive">
                                <Nav className="navbar-nav ml-auto">
                                    {props.navItems}
                                </Nav>
                            </div>
                        </div>
                    </nav>
                );
            return null;
        }
        
        // determine nav object list based off state location
        const navObj = this.determineNav(this.state.location);
        
        // build navItem list
        let navItemList = (navObj === null ? null : this.generateAndReturnNavItems(navObj));
        
        // determine navbar brand link
        const brandLink = this.determineBrandLink(this.state.location);
        
        return (
            <NavBarContainer navItems={navItemList} navbarBrandLink={brandLink}/>
        );
    }
}

export default Navbar;