import React from 'react';

function MediaArticlePreview(props) {
    // this function will return a column with a recent article component inside it
    const OrangeContainer = (props) => (
        <div className="col-12" style={{backgroundColor: "#f05f41"}}>
            <article className="p-5" style={{color: "#fff"}}>
                {props.children}
            </article>
        </div>
    );
    const WhiteContainer = (props) => (
        <div className="col-12">
            <article className="p-5">
                {props.children}
            </article>
        </div>
    );
    const Content = () => (
        <div>
            <h4>{props.title}</h4>
            <p>By {props.authorName} on {props.pubDate}</p>
            <p>{props.content}</p>
        </div>
    );
    
    // determine which container to use
    const Output = () => (props.containerColor === "orange"? <OrangeContainer><Content /></OrangeContainer> : <WhiteContainer><Content /></WhiteContainer>);
                          
    return (
        <Output />
    );
}

export default MediaArticlePreview;