import actionTypes from '../actions/actionTypes';

export default (state = {
    filteredProducts: [],
    filterObject: {}
}, action) => {
    const newState = Object.assign({}, state);
    switch (action.type) {
        case actionTypes.FETCH_BASE_FILTER_OPTIONS:
            newState.filterObject = action.payload;
            return newState;
        case actionTypes.ADD_PRODUCT_TO_FILTER_LIST:
            if(!newState.filteredProducts.includes(action.payload))
                newState.filteredProducts.push(action.payload);
            return newState;
        case actionTypes.REMOVE_PRODUCT_FROM_FILTER:
            if(newState.filteredProducts.includes(action.payload)) {
                const newList = newState.filteredProducts.filter(val => val !== action.payload);
                newState.filteredProducts = newList;
            }
            return newState;
        case actionTypes.RESET_PRODUCT_FILTER:
            newState.filteredProducts = new Array();
            return newState;
        default:
            return state;
    }
};