import React from 'react';
import { Router, Route, Switch, Prompt } from 'react-router-dom';
import { withRouter } from 'react-router';
import { connect } from 'react-redux';
import { fetchCartProducts } from './actions/cartActions';
import history from './history';
import NavPrompt from './NavPrompt';

// UTILITY CLASS IMPORTS
import CartUtility from './utilityComponents/Cart';

// UI IMPORTS
import Navbar from './components/navbar/Navbar.js';
import HomePage from './components/homepage/HomePage.js';
import Store from './components/storePage/StorePage.js';
import ProductSearch from './components/searchPage/SearchPage.js';
import CategoryPage from './components/productCategory/CategoryPage.js';
import ViewProductPage from './components/productViewPage/ProductPage.js';
import Cart from './components/cart/CartPage.js';
import Login from './components/loginPage/Login.js';

// STYLES IMPORTS
import './App.css';

class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            shouldRenderPrompt: false,
        };
    }
    
    componentDidMount() {
        console.log('app has mounted')
        this.props.fetchCartProducts();
    }
    
    render() {
        return (
            <div className="AppContainer">
                <Router history={history}>
                    <div>
                        <Route 
                            children={ props => <Navbar {...props}/> } 
                        />
                        <Switch>
                            <Route path="/" exact component={HomePage} />
                            <Route path="/home" component={HomePage} />
                            <Route path="/store" component={Store} />
                            <Route path="/search" component={ProductSearch} />
                            <Route path="/productCategory/:category" component={CategoryPage} />
                            <Route path="/products/:productId" component={ViewProductPage} />
                            <Route path="/cart" component={Cart} />
                            <Route path="/login" component={Login} />
                        </Switch>
                    </div>
                </Router>
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
    };
}
  
export default connect(
    mapStateToProps, 
    { fetchCartProducts }
)(App);
