import React from 'react';

function CartHeader(props) {
    return (
        <div>
            <h1 className="mb-4 text-light">Your Cart</h1>
            <div className="row cartHeaderRow mb-2 cartHeadingStyle text-light">
                <div className="pl-5 col-sm-4 col-md-6 col-lg-4 col-xl-4 align-self-center">
                    <h4>Product</h4>
                </div>
                <div className="pl-5 col-sm-3 col-md-2 col-lg-2 col-xl-2 align-self-center">
                    <h4>Price</h4>
                </div>
                <div className="pl-5 col-sm-3 col-md-2 col-lg-2 col-xl-2 align-self-center">
                    <h4>Quantity</h4>
                </div>
                <div className="pl-5 col-sm-2 col-md-2 col-lg-2 col-xl-2 align-self-center">
                    <h4>Item Total</h4>
                </div>
            </div>
        </div>
    );
}

export default CartHeader;