import React from 'react';
import { Link } from 'react-router-dom';

// REUSEABLE COMPONENTS
import HomeHeader from './HomeHeader.js';
import Divider from '../sharedComps/Divider.js';
import UniversalButton from '../sharedComps/UniversalButton.js';
import Footer from '../sharedComps/Footer.js';

// SERVICES COMPONENT
import ServicesSection from './ServicesSection.js';
import ServiceHoverImages from '../sharedComps/CategoryHoverSection.js';

// MEDIA COMPONENTS
import MediaSection from './MediaSection.js';
import VidGroup from './MediaVidGroup.js';
import VidThumbnail from './MediaVidThumbnail.js';
import ArticleGroup from './MediaArticleGroup.js';
import ArticlePreview from './MediaArticlePreview.js';

// SPONSOR COMPONENTS
import SponsorSection from './SponsorsSection.js';
import SponsorsImage from './SponsorsImage.js';

// IMAGE IMPORTS FOR HOVER SECTION
import ClothingThumbnail from '../../assets/img/clothingRef.jpg';
import PodcastThumbnail from '../../assets/img/podcastRef.jpg';
import BitcoinThumbnail from '../../assets/img/bitcoinRef.jpg';

// TEMP SPONSOR IMAGE IMPORTS (DELETE LATER)
import Image1 from '../../assets/img/brccIconRef.jpg';
import Image2 from '../../assets/img/cabelasRef.png';
import Image3 from '../../assets/img/goaRef.png';
import Image4 from '../../assets/img/remingtonRef.png';
import Image5 from '../../assets/img/trumpMAGARef.jpg';
import Image6 from '../../assets/img/dailyWireRef.png';

class HomePage extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            serviceHoverCategories: [
            {
                endPoint: "",
                thumbnail: ClothingThumbnail,
                category: "Store",
                heading: "Premium Products"
            },
            {
                endPoint: "",
                thumbnail: PodcastThumbnail,
                category: "Media",
                heading: "Podcasts and News"
            },
            {
                endPoint: "",
                thumbnail: BitcoinThumbnail,
                category: "Services",
                heading: "Pay-by-Crypto"
            }
        ],
            recentArticles: [
                {
                    title: "Article One",
                    authorName: "Bob Dylan",
                    pubDate: "12/13/2019",
                    textContent: "Lorem ipsum dolor sit amet, consectetuer adipiscing elit, sed diam nonummy nibh euismod tincidunt ut laoreet dolore magna aliquam erat volutpat. Ut wisi enim ad minim veniam, quis nostrud exerci tation ullamcorper suscipit lobortis nisl ut aliquip ex ea commodo consequat. Duis autem vel eum iriure dolor in Lorem ipsum dolor sit amet, consectetuer adipiscing elit, sed diam nonummy nibh euismod tincidunt ut laoreet dolore magna aliquam erat volutpat. Ut wisi enim ad minim veniam, quis nostrud exerci tation ullamcorper suscipit lobortis nisl ut aliquip ex ea commodo consequat. Duis autem vel eum iriure dolor in",
                    containerColor: "orange",
                },
                {
                    title: "Article Three",
                    authorName: "Iron Man",
                    pubDate: "12/14/2019",
                    textContent: "Lorem ipsum dolor sit amet, consectetuer adipiscing elit, sed diam nonummy nibh euismod tincidunt ut laoreet dolore magna aliquam erat volutpat. Ut wisi enim ad minim veniam, quis nostrud exerci tation ullamcorper suscipit lobortis nisl ut aliquip ex ea commodo consequat. Duis autem vel eum iriure dolor in Lorem ipsum dolor sit amet, consectetuer adipiscing elit, sed diam nonummy nibh euismod tincidunt ut laoreet dolore magna aliquam erat volutpat. Ut wisi enim ad minim veniam, quis nostrud exerci tation ullamcorper suscipit lobortis nisl ut aliquip ex ea commodo consequat. Duis autem vel eum iriure dolor in",
                    containerColor: "white",
                },
                {
                    title: "Article Two",
                    authorName: "Ryan Reynolds",
                    pubDate: "12/15/2019",
                    textContent: "Lorem ipsum dolor sit amet, consectetuer adipiscing elit, sed diam nonummy nibh euismod tincidunt ut laoreet dolore magna aliquam erat volutpat. Ut wisi enim ad minim veniam, quis nostrud exerci tation ullamcorper suscipit lobortis nisl ut aliquip ex ea commodo consequat. Duis autem vel eum iriure dolor in Lorem ipsum dolor sit amet, consectetuer adipiscing elit, sed diam nonummy nibh euismod tincidunt ut laoreet dolore magna aliquam erat volutpat. Ut wisi enim ad minim veniam, quis nostrud exerci tation ullamcorper suscipit lobortis nisl ut aliquip ex ea commodo consequat. Duis autem vel eum iriure dolor in",
                    containerColor: "orange",
                },
            ],
            recentVideos: [
                {
                    vidURL: "https://www.youtube.com/embed/2FhJmewvsZ0"
                },
                {
                    vidURL: "https://www.youtube.com/embed/byL_rW8n-Ow"
                },
                {
                    vidURL: "https://www.youtube.com/embed/JAgpNdbnHLw"
                },
            ],
            sponsorsImage:[
                Image1,
                Image2,
                Image3,
                Image4,
                Image5,
                Image6
            ]
        };
    }
    
    render() {
        // copy recent articles and video state arrays
        // maps each of the recent arr objects to their respective JSX components
        let articleArrCopy = [...this.state.recentArticles];
        const RecentArticles = articleArrCopy.map((article, index) => 
            <ArticlePreview 
                key={article.title}
                title={article.title}
                authorName={article.authorName}
                pubDate={article.pubDate}
                content={article.textContent}
                containerColor={article.containerColor}
            />
         );
        
        let videoArrCopy = [...this.state.recentVideos];
        const RecentVideos = videoArrCopy.map((video, index) => 
            <VidThumbnail 
                key={video.title}
                vidLink={video.vidURL}
            />
         );
        
        let sponsorArrCopy = [...this.state.sponsorsImage];
        const Sponsors = sponsorArrCopy.map((currentImage)=>
            <SponsorsImage image={currentImage} />
         );
        
        return (
            <div>
                <HomeHeader />
            
                <Divider id={"about"} dividerColor={"orange"} customClass="scrollSpySection">
                    <div className="row">
                        <div className="col offset-lg-8 mx-auto text-center">
                            <h2 className="text-white section-heading">Who We Are</h2>
                            <hr className="light my-4"/>
                            <p className="text-faded mb-4">Freedom Incorporated is a company dedicated to preserving and promoting American values and principles. Our team believes deeply in the rights enumerated in the US Constitution, with our products reflecting that truth. Most importantly,however, is the fact that we value critical thinking, pragmatism, and the application of cutting-edge technologies to ensure freedom and liberty across the United States.</p>
                            <UniversalButton buttonColor={"white"} buttonText={"checkout our yt!"} buttonLink={"/"}/>
                        </div>
                    </div>
                </Divider>
            
                <ServicesSection />
                <ServiceHoverImages sectionId={"servicesOverview"} hoverImages={this.state.serviceHoverCategories} />
            
                <Divider id={"store"} dividerColor={"dark"} customClass={" text-light text-center"}>
                    <h2 className="mb-4">Checkout Our Store</h2>
                    <UniversalButton buttonColor={"white"} buttonText={"start shopping!"} buttonLink={"/store"}/>
                </Divider>
            
                <MediaSection>
                    <VidGroup>
                        {RecentVideos}
                    </VidGroup>
                    <ArticleGroup>
                        {RecentArticles}
                    </ArticleGroup>
                </MediaSection>
            
                <SponsorSection>
                    {Sponsors}
                </SponsorSection>
            
                <Footer />
            </div>
        );
    }
}

export default HomePage;