import React from 'react';

// UI COMPONENT IMPORTS
import CategoryImage from './CategoryHoverImage.js';

class ServicesOverview extends React.Component {
    // class takes in an array of hover category objects that contain: an endPoint, thumbnail, category text, and heading text
    constructor(props) {
        super(props);
        this.state={};
        
        // binds
        this.generateAndReturnImages = this.generateAndReturnImages.bind(this);
    }
    
    generateAndReturnImages(serviceCatsObj) {
        // takes in a service categories and build a series of Navitem JSX elements
        let navItems = new Array();
        for(let i = 0; i < serviceCatsObj.length; i++) {
            let currentNavItem = serviceCatsObj[i];
            navItems.push(
                <div className="col-sm-6 col-lg-4">
                    <CategoryImage
                        key={i.toString()}
                        endPoint={currentNavItem.endPoint}
                        thumbnail={currentNavItem.thumbnail}
                        category={currentNavItem.category}
                        heading={currentNavItem.heading}
                    /> 
                </div>
            );
        }
        return navItems;
    }
    
    render() {
        const SectionContainer = (props) => (
            <section id={this.props.sectionId} className="p-0">
                <div className="container-fluid p-0">
                    <div className="row no-gutters popup-gallery">
                        {props.sectionItems}
                    </div>
                </div>
            </section>
        );
        const navItemList = this.generateAndReturnImages(this.props.hoverImages);
        
        return (
            <SectionContainer sectionItems={navItemList} />
        );
    }
}

export default ServicesOverview;