let Cart = (()=>{
    const STORAGE_KEY = "OPES_SHOPPING_CART";
    return class {
        // this is a utility class meant to add and remove products from local storage
        /*static bundleProduct(productId, parsedProductResults, productQuantity) {
            // takes in a product id, parsed product results, and quantity
            // builds objects with the method parameters as properties
            const productBundle = {};
            productBundle.id = productId;
            productBundle.selectedOptions = parsedProductResults;
            productBundle.quantity = productQuantity;

            return productBundle;
        }*/
        
        static bundleProduct(productData, selectedQuantity, selectedConfiguration) {
            // takes in a product id, parsed product results, and quantity
            // builds objects with the method parameters as properties
            const cartObj = {};
            cartObj.id = productData.id;
            cartObj.itemName = productData.name;
            cartObj.imageLink = productData.imageURL;
            cartObj.price = productData.price;
            cartObj.quantity = selectedQuantity;
            cartObj.selectedOptions = selectedConfiguration;
            cartObj.quantityLimit = productData.quantityLimit;
            return cartObj;
        }
        
        static isItemStillValid(productData, productBundle) {
            // method will compare a product bundle to data retrieved from server to determine if object is still valid
            // check number of properties
            const bundleLength = Object.keys(productBundle).length;
            if(bundleLength > 7 || bundleLength < 7)
                return false
            if(productBundle.quantityLimit != productData.quantityLimit)
                return false
            if(productBundle.quantity > productData.quantityLimit)
                return false;
            if(productBundle.itemName != productData.name)
                return false
            if(productBundle.price != productData.price)
                return false
            // return true if no changes were detected
            return true;
        }
        
        static initializeProductCart() {
            // add array to storage cart if products do not exist
            if(Cart.doProductsExist(Cart.getAllProductsInCart()) !== true) {
                const baseDataStruct = new Array();
                const JSONItem = JSON.stringify(baseDataStruct);
                localStorage.setItem(STORAGE_KEY,JSONItem);
            }
        }

        static addProductToCart(productBundle, cartArr) {
            // add item to the cart
            // check if storage group is available
            // copy array and then push bundle onto data and set to storage
            if(cartArr.length === 0) {
                cartArr = [productBundle];
            } else {
                cartArr.push(productBundle);
            }
            return cartArr;
        }
        
        static removeProductById(productId, cartArr) {
            // remove item by search for id
            let newArr = new Array();
            cartArr.forEach((product)=>{
                if(product.id != productId)
                    newArr.push(product);
            });
            return newArr;
        }

        static getProductById(productId, cartArr) {
            // get item from cart by product id
            let foundProduct = null;
            cartArr.forEach((product)=>{
                if(product.id == productId)
                    foundProduct = product;
            });
            return foundProduct;
        }
        
        static updateQuantityById(productId, quantity, cartArr) {
            // find item
            // set quantity
            // return array
            for(let i =0; i < cartArr.length; i++) {
                const item = cartArr[i];
                if(item.id == productId) {
                    item.quantity = quantity;
                    return cartArr;
                } 
            }
            return cartArr;
        }
        
        static doesProductConfigExist(productBundle, cartArr) {
            // this method will check to see if the requested product configuration has already been added to the stored cart
            let doesExist = false;
            // loop through cart products
            cartArr.forEach((product)=>{
                const productConfig = product.selectedOptions;
                let configSameCounter = 0;
                // loop through individual option configuration
                productConfig.forEach((option)=>{
                    const bundleConfig = productBundle.selectedOptions;
                    // loop through productBundle's option configuration
                    bundleConfig.forEach((selectedOption)=>{
                        // increment counter if the options are the same for both the bundle and cart product
                        if(selectedOption.value == option.value)
                            configSameCounter+=1;
                    });
                });
                // check if the same number of equal options exist
                if(configSameCounter === productBundle.selectedOptions.length)
                    doesExist = true;
            });
            return doesExist;
        }

        static doProductsExist(cartArr) {
            // check if item is in storage by key
            // return bool
            if(cartArr !== null && cartArr.length !== 0)
                return true;
            else
                return false; 
        }
        
        static saveProductsToStorage(cartArr) {
            const finalJSONArr = JSON.stringify(cartArr);
            localStorage.setItem(STORAGE_KEY,finalJSONArr);
        }
        
        static getAllProductsInCart() {
            return JSON.parse(localStorage.getItem(STORAGE_KEY));
        }
    }
})();

// initialize the cart
Cart.initializeProductCart();

export default Cart;