import React from 'react';
import { connect } from 'react-redux';

// UI COMPONENTS
import ProductGrid from '../sharedComps/ProductGrid';
import ProductFilter from '../sharedComps/ProductFilter.js';

function SearchProducts(props) {
    // parse product data array
    const productData = props.products.searchProducts;
    const filteredProducts = props.filterList.filteredProducts;
    
    console.log('custom grid has re-render')
    console.log(props.filterList)
    
    const filteredProductData = () => {
        let validProductArr = new Array();
        productData.forEach((productData)=>{
            const productId = productData.id;
            let isProductFiltered = false;
            // check if the product is not filtered
            filteredProducts.forEach((id)=>{
                if(id == productId) {
                    isProductFiltered = true;
                }
            });
            // add product to valid array
            if(!isProductFiltered)
                validProductArr.push(productData);
        });
        return validProductArr;
    };
    
    const areInputValsValid = () => {
        let isValid = false;
        if(productData !== undefined && filteredProducts !== undefined && props.products !== undefined && props.currentCategory !== undefined) {
            isValid = true;
        }
        return isValid;
    };
    
    console.log('logging filtered data')
    console.log(areInputValsValid()===true?filteredProductData() : null)
    
    return (
        <>
            { areInputValsValid() === true ?
                <>
                    { props.products.searchProducts.length !== 0 ?
                        <div className="row mb-3">
                            <div className="col-12 mb-2">
                                <ProductFilter currentPathname={props.currentCategory} />
                            </div>
                        </div> : null
                    } <ProductGrid products={filteredProductData()}/>
                </> : null
            }
        </>
    );
}

const mapStateToProps = state => {
    return {
        filterList: state.productFilter,
        products: state.searchProducts,
    };
}
  
export default connect(
    mapStateToProps
)(SearchProducts);