import DBProducts from '../FakeAPI/DBProducts';
import DBInventory from '../FakeAPI/DBInventory';
import DBContent from '../FakeAPI/DBContent';
import DBFilters from '../FakeAPI/DBFilters';
import axios from 'axios';
require('dotenv').config();

// SERVER CONNECTION
export const freedomIncAPI = axios.create({
    baseURL: process.env.REACT_APP_DEV_ORIGIN,
    headers: {
        'Access-Control-Allow-Origin': '*',
        'Content-Type': 'application/json',
    }
});

// PRODUCT SERVER METHODS -------------------------------------------->
export async function getProductsBySearchString(searchString) {
    // TODO: make fetch to server to search for products
    let dbProducts = new DBProducts();
    const results = dbProducts.SearchFromProducts(searchString);
    return results;
}

export async function getProductsByCategory(categoryString) {
    // TODO: make fetch to server for category products
    let dbProducts = new DBProducts();
    const fakeProducts = dbProducts.GetProductsByCategory(categoryString);
    //console.log('logging products and input string in server')
    //console.log(categoryString)
    //console.log(fakeProducts)
    return fakeProducts;
}

export async function isProductAvailable(productId, optionConfiguration) {
    // TODO: make fetch to server for availability
    return true;
}

export async function getProductDataById(productId) {
    // TODO: make fetch to server for product
    let dbProducts = new DBProducts();
    const fakeProduct = dbProducts.GetProductById(productId);
    return fakeProduct;
}

export async function getInventoryById(productId) {
    // TODO: make fetch to server for product inventory
    let dbInventory = new DBInventory();
    const fakeInventory = dbInventory.GetInventoryByProductId(productId);
    return fakeInventory;
}

export async function getProductPriceByConfigAndId(productId, optionConfiguration) {
    // TODO: make fetch to server for product price
    const product = this.getProductDataById(productId);
    return product.price;
}

// PRODUCT FILTER METHODS -------------------------------------------->
export async function getBaseFilter(categoryString) {
    let dbFilter = new DBFilters();
    const baseOptions = dbFilter.GetBaseFilter();
    return baseOptions;
}

// CONTENT SERVER METHODS -------------------------------------------->
export async function getCategoryImage(categoryString) {
    // TODO: MAKE FETCH TO SERVER FOR CATEGORY IMAGE
    let dbContent = new DBContent();
    const image = dbContent.GetImageByCategory(categoryString);
    return image;
}

// SHIPPING SERVER METHODS -------------------------------------------->
export async function validateResidence(locationObject) {
    try {
        const response = await axios.post('/shipping/validateResidence', { locationObject });
        return response.data;
    } catch(err) {
        console.log(err);
    }
}

export default {isProductAvailable, getProductDataById, getProductPriceByConfigAndId, validateResidence, getProductsByCategory, getCategoryImage, getBaseFilter};