import actionTypes from './actionTypes';
import * as Server from '../utilityComponents/Server';

export const fetchProductsByCategory = (categoryString) => async dispatch => {
    // get product from server
    const products = await Server.getProductsByCategory(categoryString);
    
    // determines if there are no products in category
    const EMPTY = {};
    const payloadValue = products === (null || undefined) ? EMPTY : { categoryProducts: products };
    
    dispatch({
        type: actionTypes.FETCH_PRODUCTS_BY_CATEGORY,
        payload: payloadValue
    });
}