import React from 'react';
import { connect } from 'react-redux';
import { fetchProductsByCategory } from '../../actions/productCategoryActions';
import { fetchPageContentByCategory } from '../../actions/sectionContentActions';

// UTILITY IMPORTS
//import FilterConstants from '../../utilityComponents/FilterConstants.js';

// UI COMPONENTS
import ProductFilter from '../sharedComps/ProductFilter.js';
import Divider from '../sharedComps/Divider.js';
import UniversalButton from '../sharedComps/UniversalButton.js';
import Footer from '../sharedComps/Footer.js';
import ProductGrid from '../sharedComps/ProductGrid';

class CategoryPage extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            currentCategory: this.props.match.params.category,
        };
        
        this.DEFAULT_IMAGE = "https://dslv9ilpbe7p1.cloudfront.net/auU7e7rqnAiKOkyMkOGYFQ_store_banner_image.png";
    }
    
    componentDidMount() {
        this.props.fetchProductsByCategory(this.state.currentCategory);
        this.props.fetchPageContentByCategory(this.state.currentCategory);
    }
    
    render() {
        // preventing premature rendering errors
        let backgroundImage = "";
        if(Object.keys(this.props.sectionContent).length !== 0) {
            backgroundImage = this.props.sectionContent.sectionImage === (null || undefined) ? this.DEFAULT_IMAGE : this.props.sectionContent.sectionImage;
            console.log('logging background image: ' + backgroundImage)
        }
        // parse product data array
        const productData = this.props.productData.categoryProducts;
        return (
            <div>
                <div data-bs-parallax-bg="true" style={{height: "500px", backgroundImage: "url("+backgroundImage+")", backgroundPosition: "center",backgroundSize: "cover"}}>
                    <header>
                        <div className="jumbotron pulse animated hero-nature carousel-hero" style={{backgroundColor: "transparent",}}>
                            <h1 style={{textTransform: "capitalize"}} className="hero-title d-flex justify-content-lg-start customPadding">{this.state.currentCategory}</h1>
                        </div>
                    </header>
                </div>

                <Divider id={"products"} dividerColor={"light"}>
                    <div className="row mb-3">
                        <div className="col-12 mb-2">
                            <ProductFilter currentPathname={this.state.currentCategory} />
                        </div>
                    </div>
                        <ProductGrid products={productData} />
                </Divider>

                <Divider id={"backToTop"} dividerColor={"dark"} customClass={"text-center text-light"}>
                    <h2 class="mb-4">Done Viewing?</h2>
                    <UniversalButton buttonColor={"white"} buttonText={"back to top"} buttonLink={"#products"} />
                </Divider>
            
                <Footer />
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        productData: state.categoryProducts,
        sectionContent: state.sectionContent,
    };
}
  
export default connect(
    mapStateToProps, 
    { fetchProductsByCategory,
    fetchPageContentByCategory }
)(CategoryPage);