import React from 'react';

function SlideShowHeader(props) {
    return (
        <header id="carousel">
        <div className="carousel slide" data-ride="carousel" id="carousel-1">
            <div className="carousel-inner" role="listbox">
                <div className="carousel-item active">
                    <div className="jumbotron pulse animated hero-nature carousel-hero customBackground">
                        <h1 className="hero-title">Mid-Length Barrels</h1>
                        <p className="hero-subtitle">Affordable, quality, AR15 upper assemblies.</p>
                        <p><a className="btn btn-light btn-xl sr-button" role="button" data-aos="zoom-in" data-aos-duration="400" data-aos-once="true" href="#">view item</a></p>
                    </div>
                </div>
                <div className="carousel-item">
                    <div className="jumbotron pulse animated hero-photography carousel-hero">
                        <h1 className="hero-title">Signature Drinks</h1>
                        <p className="hero-subtitle">Our energy drinks, supporting your constitutional rights, with assorted, unique, flavors.</p>
                        <p><a className="btn btn-light btn-xl sr-button" role="button" data-aos="zoom-in" data-aos-duration="400" data-aos-once="true" href="#">view item</a></p>
                    </div>
                </div>
                <div className="carousel-item">
                    <div className="jumbotron pulse animated hero-technology carousel-hero">
                        <h1 className="hero-title">New Clothes</h1>
                        <p className="hero-subtitle">Nullam id dolor id nibh ultricies vehicula ut id elit. Cras justo odio, dapibus ac facilisis in, egestas eget quam.</p>
                        <p><a className="btn btn-light btn-xl sr-button" role="button" data-aos="zoom-in" data-aos-duration="400" data-aos-once="true" href="#">view item</a></p>
                    </div>
                </div>
            </div>
            <div>
                <a className="carousel-control-prev" href="#carousel-1" role="button" data-slide="prev"><i className="fa fa-chevron-left"></i><span class="sr-only">Previous</span></a><a class="carousel-control-next" href="#carousel-1" role="button" data-slide="next"><i class="fa fa-chevron-right"></i><span className="sr-only">Next</span></a>
            </div>
            <ol
                className="carousel-indicators">
                <li data-target="#carousel-1" data-slide-to="0" className="active"></li>
                <li data-target="#carousel-1" data-slide-to="1"></li>
                <li data-target="#carousel-1" data-slide-to="2"></li>
                </ol>
        </div>
    </header>
    );
}

export default SlideShowHeader;