import React from 'react';
import { connect } from 'react-redux';
import { fetchProductDataById, resetProductData } from '../../actions/viewProductActions';
import { applyPriceAddon, resetPriceAddon } from '../../actions/productPriceActions';
import { resetBanner } from '../../actions/messageBannerActions';

// REUSABLE COMPONENT IMPORTS
import Divider from '../sharedComps/Divider.js';
import Footer from '../sharedComps/Footer.js'

// PRODUCT COMPONENT IMPORTS
import ProductOverview from './ProductOverview.js';
import ProductViewIO from './ProductIO.js';
import ProductDesc from './ProductDescription.js';

class ProductPage extends React.Component {
    constructor(props) {
        super(props);
    }
    
    componentDidMount() {
        // get product id from path
        // fill redux store with product
        const productId = this.props.match.params.productId;
        this.props.fetchProductDataById(productId);
        this.props.resetPriceAddon();
        this.props.resetBanner();
    }
    
    componentWillUnmount() {
        // reset product view data
        this.props.resetProductData();
        this.props.resetPriceAddon();
        this.props.resetBanner();
    }
    
    render() {
        return (
            <div>
                <Divider id={"productView"} dividerColor={"dark"} customClass={"mt-5 mb-5"}>
                    {
                        this.props.productData !== null ?
                        <div>
                            <div className="mb-5">
                                <ProductOverview />
                                <ProductViewIO />
                                <ProductDesc descripText={this.props.productData.productData.description} />
                            </div>
                        </div>
                        : null
                    }
                </Divider>
                <Footer />
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        cart: state.cart,
        productData: state.viewProduct,
    };
}
  
export default connect(
    mapStateToProps, 
    { fetchProductDataById, 
     resetProductData,
     applyPriceAddon, 
     resetPriceAddon, 
     resetBanner }
)(ProductPage);