import React from 'react';

class OptionDropdown extends React.Component {
    constructor(props) {
        super(props)
    }
    
    render() {
        return (
            this.props.options.map((dropdown)=>
                <div key={dropdown.optionName} className="col-sm-6 col-md-6 col-lg-4 mb-4">
                    <div className="form-group">
                        <label className="text-light">{dropdown.optionName}</label>
                        <br />
                        <select className="form-control" id={dropdown.optionName}>
                            <option value="0">Select One</option>
                            {
                                dropdown.optionArray.map((option)=> {
                                    const optionEl = <option key={option} value={option}>{option}</option>;
                                    const optionDisabledOS = <option style={{color:'white', backgroundColor:'lightGrey'}} key={option} value={option}>{option + " - Out Of Stock"}</option>;
                                    let returnedJSX = optionEl; // default for option
                                    // conditional logic needs to be repetative like this null represents empty, and permanent and temp stock are mutually exclusive
                                    if(this.props.outOfStockItems !== null || this.props.conditionalOutOfStock !== null) {
                                        if(this.props.isOptionPermOFSFunc(option) || this.props.isOptionConditionalOFSFunc(option)) {
                                            returnedJSX = optionDisabledOS;
                                        }
                                    }
                                    return returnedJSX;
                                })
                            }
                        </select>
                    </div>
                </div>
            )
        );
    }
}
export default OptionDropdown;