import { combineReducers } from 'redux';

// IMPORT ACTIONS
import cartReducer from './cartReducer';
import priceReducer from './productPriceReducer';
import productReducer from './viewProductReducer';
import bannerReducer from './messageBannerReducer';
import shippingInfoReducer from './shippingInfoReducer';
import featuredProductsReducer from './featuredProductsReducer';
import productCategoryReducer from './productCategoryReducer';
import productSearchReducer from './productSearchReducer';
import sectionContentReducer from './sectionContentReducer';
import productFilterReducer from './productFilterReducer';

export default combineReducers({
    cart: cartReducer,
    viewProduct: productReducer,
    viewProductPrice: priceReducer,
    messageBanner: bannerReducer,
    shippingInfo: shippingInfoReducer,
    sectionContent: sectionContentReducer,
    featuredProducts: featuredProductsReducer,
    categoryProducts: productCategoryReducer,
    searchProducts: productSearchReducer,
    productFilter: productFilterReducer,
});