import React from 'react';
import { connect } from 'react-redux';
import { addCartProduct, removeCartProduct, fetchCartProducts } from '../../actions/cartActions';
import { setBannerMessage } from '../../actions/messageBannerActions';

// UI IMPORTS
import Footer from '../sharedComps/Footer.js'
import Products from './Products.js';
import CartHeader from './CartHeader.js';
import Banner from '../sharedComps/MessageBanner';
import CartUserEntry from './CartUserEntry.js';

// UTILITY CLASS IMPORTS
import CartUtility from '../../utilityComponents/Cart.js';
import Server from '../../utilityComponents/Server.js';

// DELETE THESE LATER
import ProductImage from '../../assets/img/arStoreRef.jpg';
//import ZipCodeCalc from "../../utilityComponents/ZipCodeCalculator.js";

class CartPage extends React.Component {
    constructor(props) {
        super(props);
    }
    
    render() {
        // set empty cart message
        const emptyCartMessage = (<h2 className="mt-5 mb-5 text-light">It looks like your cart is empty :( </h2>);
                      
        return (
            <div>
                {
                this.props.cart.length !== 0 ?
                <div className="bg-dark">
                    <section>
                        <div className="container">
                            <Banner />
                            <CartHeader />
                            <Products />
                        </div>
                    </section>
                    <section>
                        <CartUserEntry />       
                    </section>
                </div> 

                :

                <div className="bg-dark">
                    <section>
                        <div className="container">
                            <div className="d-flex justify-content-center">
                                {emptyCartMessage}
                            </div>
                        </div>
                    </section>
                </div>
                }
                <Footer />
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        cart: state.cart
    };
}
  
export default connect(
    mapStateToProps //, 
    //{ addCartProduct, removeCartProduct, setBannerMessage, fetchCartProducts }
)(CartPage);