import React from 'react';

function Divider(props) {
    // this function will return a divider with capable of accepting any type of component content
    
    const DarkContainer = () => (
        <section id={props.id} className={"bg-dark "}>
            <div className={"container " + props.customClass}>
                {props.children}
            </div>
        </section>
    );
    
    const OrangeContainer = () => (
        <section id={props.id} className={"bg-primary "}>
            <div className={"container " + props.customClass}>
                {props.children}
            </div>
        </section>
    );
    
    const LightContainer = () => (
        <section id={props.id}>
            <div className={"container " + props.customClass}>
                {props.children}
            </div>
        </section>
    );
    
    const Content = () => props.children; // getting component children from main divider function
    
    let Output = () => { // generate the output
        if(props.dividerColor === "orange")
            return (
                <OrangeContainer id={props.id} customClass={props.customClass}>
                    <Content />
                </OrangeContainer>
            )
        else if(props.dividerColor === "dark")
            return (
                <DarkContainer id={props.id} customClass={props.customClass}>
                    <Content />
                </DarkContainer>
            )
        else
            return (
                <LightContainer id={props.id} customClass={props.customClass}>
                    <Content />
                </LightContainer>
            )
    }
    
    return(
       <Output /> 
    );
}
        
export default Divider;