import React from 'react';
import { connect } from 'react-redux';
import { removeCartProduct, updateQuantityById } from '../../actions/cartActions';
import { setBannerMessage } from '../../actions/messageBannerActions';

// UI IMPORTS
import UniversalButton from '../sharedComps/UniversalFunctionalButton.js';

// UTILITY CLASS IMPORTS
import CartUtility from '../../utilityComponents/Cart.js';
import Server from '../../utilityComponents/Server.js';

class Product extends React.Component {
    constructor(props) {
        super(props);
        
        this.COMPONENT_STYLE_COLOR = 'light';
        
        // BINDS
        this.removeProductClickEvent = this.removeProductClickEvent.bind(this);
        this.quantityChangeEvent = this.quantityChangeEvent.bind(this);
        this.isQuantityValidate = this.isQuantityValidate.bind(this);
        this.generateCartItems = this.generateCartItems.bind(this);
        this.initializeCartContent = this.initializeCartContent.bind(this);
        this.getProductQuantityById = this.getProductQuantityById.bind(this);
    }
    
    componentDidMount() {
        this.initializeCartContent();
    }
    
    // EVENT HANDLERS
    removeProductClickEvent(e) {
        // get target event
        // get the value of the button, which is set to the product id
        // remove product from local storage
        // remove product from state
        const target = e.target;
        const productId = target.value;
        const successAlert = "success";
        const message = "Item has been removed!";
        this.props.removeCartProduct(productId, [...this.props.cart]);
        this.props.setBannerMessage(message,successAlert);
    }
    
    quantityChangeEvent(e) {
        // get target event
        // get value of input field
        // if quantity is invalid
            // set the element to old product quantity
        // else quantity is valid
            // update state of specific product by id
        // recalculate pricing
        const target = e.target;
        const productId = target.getAttribute("data-productId");
        const quantityVal = target.value;
        if(!this.isQuantityValidate(productId,quantityVal)) {
            const defaultSelectedQuantity = this.getProductQuantityById(productId);
            target.value = defaultSelectedQuantity;
        }
        else {
            this.props.updateQuantityById(productId,quantityVal, [...this.props.cart]);
        }
    }
    
    // CLIENT SIDE PARSING AND VALIDATION METHODS
    isQuantityValidate(productId,quantity) {
        // this method will take in a product id and quantity from the UI
        // will check find product by id (in local state)
        // then the quantity will be checked against the quantityLimit property
        // if in bounds return true
        // else return false
        if(quantity !== "") {
            //const stateProduct = this.getStateProductById(productId);
            const stateProduct = CartUtility.getProductById(productId,[...this.props.cart]);
            const quantityLimit = stateProduct.quantityLimit;
            if(quantity<= quantityLimit && quantity > 0)
                return true;
        }
        return false;
    }
    
    getProductQuantityById(productId) {
        // this method searches the cart items for the matching product
        // returns the selected quantity number
        let defaultQuantity = 1;
        const cartCopy = [...this.props .cart];
        cartCopy.forEach((product)=>{
            if(product.productId == productId)
                defaultQuantity = product.quantity;
        });
        return defaultQuantity;
    }
    
    // CART STARTUP METHODS
    async initializeCartContent() {
        console.log('initializeCartContent has fired')
        // this method grabs products out of local storage and pushes them into the cart if available
        // get products from local storage
        // if products from local storage not null
            // fetch availability of product by Id and configuration
            // if fetch to availability returns true
                // fetch product data by Id
                // fetch price by Id AND configuration
                // parse product data and convert to cart object
            // else
                // get product name from server
                // add product name to string builder
                // remove product from local storage and ignore
            // show banner of product names that were removed
        let removedProductsMessage = "These products have been removed due to lack of availability: ";
        let haveProductsBeenRemoved = false;
        const warningAlert = "warning";
        if(this.props.cart.length !== 0) {
            for(const product of this.props.cart) {
                // parsing from local storage
                const productId = product.id;
                const productConfiguration = product.selectedOptions;
                const productQuantity = product.quantity;
                const productName = product.name;
                try {
                    // fetch product data
                    const productData = await Server.getProductDataById(productId);
                    // validate product configuration (takes care of mischevious clients editing local storage)
                    if(await Server.isProductAvailable(productId,productConfiguration)) {
                        // fetch product price data
                        const productPrice = await Server.getProductPriceByConfigAndId(productId,productConfiguration); // price needs to be retrieved from server to ensure authenticity
                        // determine if user tampered with data in local storage
                        if(!CartUtility.isItemStillValid(productData,product)) {
                            this.props.removeCartProduct(productId,this.props.cart);
                            removedProductsMessage+=(productName + ", ");
                            haveProductsBeenRemoved = true;
                        }
                    }
                    else {
                        removedProductsMessage+=(productName + ", ");
                        this.props.removeCartProduct(productId,[...this.props.cart]);
                        haveProductsBeenRemoved = true;
                    }
                } catch (e) {
                    console.log("error in retrieving product data");
                    console.log(e);
                }
            };
        }
        if(haveProductsBeenRemoved === true)
            this.props.setBannerMessage(removedProductsMessage,warningAlert);
    }
    
    // JSX GENERATOR
    generateCartItems(cartItems) {
        // determine color scheme
        const textColor = (this.COMPONENT_STYLE_COLOR === "light"? "text-dark" : "text-light");
        const backgroundColor = (this.COMPONENT_STYLE_COLOR === "light"? "bg-light" : "bg-dark");
        const borderStyle = (this.COMPONENT_STYLE_COLOR === "light"? "border border-light" : "border border-light");
        // takes in an array of cart items
        // maps items to JSX and then returns as array
        let items = cartItems.map((item)=>{
            const totalPrice = item.quantity * item.price;
            return(
                <div id="cartItems" key={item.id} className={"row cartItemRow mb-5 py-4 p-2 rounded " + backgroundColor + " " + textColor}>
                <div className="col-sm-12 col-md-12 col-lg-4 col-xl-4 align-self-center customCartRow">
                    <div className="row customCartRow">
                        <div className="col-sm-12 col-md-12 col-lg-12 col-xl-12">
                            <div className="itemImageContainer py-4 px-2">
                                <img className={"cartItemImage  " + borderStyle} src={item.imageLink}/>
                            </div>
                            <h4 className={"text-center"}>{item.itemName}<br/></h4>
                        </div>
                    </div>
                </div>
                <div className="col-12 col-sm-12 col-md-12 col-lg-2 col-xl-2 align-self-center d-flex justify-content-start p-2">
                    <p className="productPara"><b>Unit Price:</b> &#36;{item.price}</p>
                </div>
                <div className="col-sm-12 col-md-12 col-lg-2 col-xl-2 align-self-center d-flex justify-content-center p-2">
                    <input onChange={this.quantityChangeEvent} data-productId={item.id} defaultValue={item.quantity} min="1" max={item.quantityLimit} type="number" className="quantityInput mb-2"/>
                </div>
                <div className="col-12 col-sm-12 col-md-12 col-lg-2 col-xl-2 align-self-center d-flex justify-content-start p-2">
                    <p className="productPara"><b>Total:</b> &#36;{totalPrice}</p>
                </div>
                <div className="col-12 col-sm-12 col-md-12 col-lg-2 col-xl-2 align-self-center d-flex justify-content-center p-2">
                    <button onClick={this.removeProductClickEvent} className={"btn btn-light btn-xl sr-button text-light bg-primary undefined aos-init " + "removeFromCartButton"} role="button" data-aos="zoom-in" data-aos-duration="400" data-aos-once="false" value={item.id}>Remove</button>
                </div>
            </div>
            );
        })
        return items;
    }
    
    render() {
        return (
            this.generateCartItems(this.props.cart)
        );
    }
}

const mapStateToProps = state => {
    return {
        cart: state.cart
    };
}
  
export default connect(
    mapStateToProps, 
    { removeCartProduct, updateQuantityById, setBannerMessage }
)(Product);