import React from 'react';
import Lodash from 'lodash';

// STYLES IMPORT
import './SlideshowHeader.css';

// REUSEABLE COMPONENTS
import Divider from '../sharedComps/Divider.js';
import UniversalButton from '../sharedComps/UniversalButton.js';
import Footer from '../sharedComps/Footer.js';

// HEADER COMPONENTS
import SlideShowHeader from './SlideshowHeader.js';

// PRODUCTS COMPONENTS
import CategoryHoverImages from '../sharedComps/CategoryHoverSection.js';
import ProductThumbnail from '../sharedComps/ProductThumbnail.js';

// IMAGE IMPORTS
import EnergyDrinkImage from '../../assets/img/energyDrinkRef.jpg';
import ClothingImage from '../../assets/img/clothingRef.jpg';
import GearImage from '../../assets/img/firearmPartsRef.jpg';

// TEMP IMAGE IMPORTS
import tempProductImage from '../../assets/img/magaStoreRef.jpg';

// DELETE THIS LATER
import DBProducts from '../../FakeAPI/DBProducts.js';

class StorePage extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            productHoverCategories: [
            {
                endPoint: "",
                thumbnail: EnergyDrinkImage,
                category: "Consumables",
                heading: "Energy Drinks"
            },
            {
                endPoint: "",
                thumbnail: ClothingImage,
                category: "Apparel",
                heading: "Clothing"
            },
            {
                endPoint: "",
                thumbnail: GearImage,
                category: "Gear",
                heading: "Firearm Parts"
            }
        ],
            featuredProducts: null,
        };
        
        // BINDS
        this.generateProductGrid = this.generateProductGrid.bind(this);
    }
    
    componentDidMount() {
        // TODO: make AJAX call to server for list of featured products
        let dbProducts = new DBProducts();
        let productList = dbProducts.GetFeaturedProducts();
        
        if(productList !== null) {
            this.setState({
                featuredProducts: productList
            })
        }
    }
    
    generateProductGrid(productObjList) {
        // takes in an array of product objects
        // maps the list to the productThumbnail component
        // returns the mapped list
        const chunkedSubArrSize = 3;
        let listCopy = [...productObjList];
        let chunkedProducts = Lodash.chunk(listCopy,chunkedSubArrSize); // lodash allows for an array of product objects to be section in a 2D array with 3 products per sub-element
        
        // TODO: fix product link so it goes to a product view based off an ID
        const mappedProducts = chunkedProducts.map((parent)=>
            // mapping parent to build a row
            <div className="row mb-3">
            {parent.map((product, index)=>
                <ProductThumbnail
                    containerSize={index === 2 ? 'wide' : ''}
                    key={product.name + index.toString()}
                    price={product.price}
                    imageLink={product.imageURL}
                    productCategory={product.category}
                    productName={product.name}
                    productLink={"/products/"+product.id}
                />
            )}
            </div>
        );
        return mappedProducts;
    }
    
    render() {
        const productList = this.state.featuredProducts;
        return (
            <div>
                <SlideShowHeader />
                <CategoryHoverImages sectionId={"productsOverview"} hoverImages={this.state.productHoverCategories} />
            
                <Divider id={"aboutStore"} dividerColor={"light"} customClass={"text-center"}>
                    <h2 className="section-heading">About Our Store</h2>
                    <hr className="my-4" />
                    <p className="text-muted mb-4">Welcome to the Freedom Inc store! &nbsp;This is the place to purchase quality sweatshirts, T-shirts, flannels, and hats, along with AR acessories and parts kits. Love capitalism, guns, free-speech, and the feeling of freedom? Well,
                    we have unqiue America-first, 1A, and 2A clothing to put those brain cell killing commies to shame. Scroll down more info on our free shipping and carepackage services!</p>
                </Divider>
            
                <Divider id={"popularServices"} dividerColor={"orange"} customClass={"text-center"}>
                    <h2 className="text-white section-heading">Popular Services</h2>
                    <hr className="light my-4" />
                    <p className="text-faded mb-4">Due to the ubiquitous nature of online shopping, Freedom Inc has made the decision to offer FREE shipping of ALL orders over 20 dollars. Not bad, right? It gets better, we promise. On top of free shipping, the Freedom Inc store offers
                    a monthly carepackage, containing a custom phone/desktop wallpaper, a pack of our best selling Bang and Shoot energy drinks, a flannel, and a 10% discount on your next store purchase. The best part: it's all for the ripe price of 25 bucks monthly! This deal is so good, it's almost thief (kind of like socialism).<br/></p>
            
                    <UniversalButton buttonColor={"white"} buttonText={"subscribe to carepackage"} buttonLink={"/"} />
                </Divider>
                
                <Divider id={"featuredProducts"} dividerColor={"light"}>
                    <div className="row mb-5">
                        <div className="col-lg-12 text-center">
                            <h2 className="section-heading">Featured Products<br/></h2>
                            <hr className="my-4"/>
                        </div>
                    </div>
                    {productList === null ? "" : this.generateProductGrid(productList)}
                </Divider>

                <Divider id={"browseMore"} dividerColor={"dark"} customClass={"text-center text-light"}>
                    <h2 className="mb-4">Browse More Products</h2>
                    <UniversalButton buttonColor={"white"} buttonText={"view more"} buttonLink={"/"} />
                </Divider>
                <Footer />
            </div>
        );
    }
}

export default StorePage;