const actionTypes = {
    // CART ACTIONS
    ADD_PRODUCT_TO_CART: 'ADD_PRODUCT_TO_CART', // add product
    REMOVE_PRODUCT_FROM_CART: 'REMOVE_PRODUCT_FROM_CART', // remove product
    SET_CART_PRODUCTS: 'SET_CART_PRODUCTS', // set an array to the product state
    FETCH_CART_PRODUCTS: 'FETCH_CART_PRODUCTS', // get products from cart and put into redux store
    UPDATE_QUANTITY_BY_ID: 'UPDATE_QUANTITY_BY_ID',
    
    // SHIPPING PRODUCT ACTIONS
    SET_SHIPPING_PRICE: 'SET_SHIPPING_PRICE',
    
    // VIEW SINGLE PRODUCT PAGE ACTIONS
    FETCH_PRODUCT_DATA_BY_ID: 'FETCH_PRODUCT_DATA_BY_ID', // product and it's inventory on server
    RESET_PRODUCT_DATA: 'RESET_PRODUCT_DATA', // remove product and inventory data
    SET_CART_ADDON_PRICE: 'SET_CART_ADDON_PRICE', // will update default product price if option that costs extra is selected
    RESET_CART_ADDON_PRICE: 'RESET_CART_ADDON_PRICE', // reset addon price
    
    // MESSAGE BANNER ACTIONS
    SET_BANNER_MESSAGE: 'SET_CART_MESSAGE', // will show message text and alert color
    RESET_BANNER_MESSAGE: 'RESET_CART_MESSAGE', // reset message payload to none
    
    // CATEGORY PAGE ACTIONS
    FETCH_PRODUCTS_BY_CATEGORY: 'FETCH_PRODUCTS_BY_CATEGORY', // list of products from server
    FETCH_CONTENT_BY_CATEGORY: 'FETCH_CONTENT_BY_CATEGORY',
    
    // STORE MAIN PAGE ACTIONS
    FETCH_FEATURED_PRODUCTS: 'FETCH_FEATURED_PRODUCTS',
    
    // HOME PAGE ACTIONS
    FETCH_RECENT_VIDEOS: 'FETCH_RECENT_MEDIA', // lists recent video objects
    FETCH_RECENT_ARTICLES: 'FETCH_RECENT_ARTICLES', // lists recent article objects
    FETCH_SPONSORS: 'FETCH_SPONSORS', // lists sponsor images with their page links
    
    // SEARCH PAGE ACTIONS
    FETCH_PRODUCTS_BY_SEARCH_STRING: 'FETCH_PRODUCTS_BY_SEARCH_STRING', // lists products based on a string search key  
    ADD_PRODUCT_TO_FILTER_LIST: 'ADD_PRODUCT_TO_FILTER_LIST', // adds product id to filter list (i.e. products that should NOT be rendered)
    REMOVE_PRODUCT_FROM_FILTER: 'REMOVE_PRODUCT_FROM_FILTER',
    RESET_PRODUCT_FILTER: 'RESET_PRODUCT_FILTER',
    
    // PRODUCT FILTER ACTIONS
    FETCH_BASE_FILTER_OPTIONS: 'FETCH_BASE_FILTER_OPTIONS'
}

export default actionTypes;