import actionTypes from '../actions/actionTypes';

export default (state = 0, action) => {
    switch (action.type) {
        //case actionTypes.FETCH_CART_PRODUCTS:
            //return action.payload;
        case actionTypes.SET_CART_ADDON_PRICE:
            return action.payload;
        case actionTypes.RESET_CART_ADDON_PRICE:
            return action.payload;
        default:
            return state;
    }
};