import React from 'react';

// UI IMPORTS
import MediaHeader from './MediaHeader.js';

function MediaSection(props) {
    return (
        <section id="media" className="scrollSpySection">
            <div className="container">
                <MediaHeader
                    title={"Freedom Media"}
                    mainText={"Freedom Inc. recongnizes the importance of well-written, articulate, and honest news. As a result, we launched Freedom Media, a counter to mainstream news platforms that releases true journalistic pieces that follow all of the necessary best-practices that were seemingly forgotten by large news corporation. In addition to written material, Freedom Media also has a monthly podcast that covers everything from world news, cutting-edge technology, US politics, and philosophy."}
                />
                {props.children}
            </div>
        </section>
    );
}

export default MediaSection;