import actionTypes from './actionTypes';
import * as Server from '../utilityComponents/Server';

export const fetchBaseFilter = () => async dispatch => {
    // get product filter from server
    const filterObj = await Server.getBaseFilter();
    
    dispatch({
        type: actionTypes.FETCH_BASE_FILTER_OPTIONS,
        payload: filterObj
    });
}

export const addProductToFilterList = (productId) => async dispatch => {
    dispatch({
        type: actionTypes.ADD_PRODUCT_TO_FILTER_LIST,
        payload: productId
    });
}

export const removeProductFromFilterList = (productId) => async dispatch => {
    dispatch({
        type: actionTypes.REMOVE_PRODUCT_FROM_FILTER,
        payload: productId
    });
}

export const resetProductFilterList = () => async dispatch => {
    dispatch({
        type: actionTypes.RESET_PRODUCT_FILTER,
        payload: []
    });
}