import React from 'react';
import { connect } from 'react-redux';
import { applyPriceAddon, resetPriceAddon } from '../../actions/productPriceActions';
import { resetBanner, setBannerMessage } from '../../actions/messageBannerActions';

// UI IMPORTS
import Banner from '../sharedComps/MessageBanner';

class ProductOverview extends React.Component {
    constructor(props) {
        super(props);
    }
    
    componentDidMount() {
        console.log('product overview page re-mounted');
    }
    
    render() {
        // UI variables
        let statusString = "";
        let price = "";
        let productName = "";
        let imageURL = "";
        // redux store objects
        const inventory = this.props.product.inventoryData;
        const product = this.props.product.productData;
        if(product !== null) {
            // parse product data
            if(!(inventory.currentQuantity <= 0))
                statusString = "In Stock";
            else
                statusString = "Out of Stock";
            if(this.props.productPrice !== 0)
                price = (product.price + this.props.productPrice).toFixed(2);
            else
                price = product.price;
            productName = product.name; 
            imageURL = product.imageURL;
        }
        return (
            <div>
                <Banner />
                <div className="row">
                    <div className="col-sm-12 col-md-6 col-lg-4 d-flex mb-3">
                        <div className="imageContainer">
                            <img className="productImageStyle" src={imageURL}/>
                        </div>
                    </div>
                    <div className="col-sm-12 col-md-6 col-lg-8">
                        <div className="row">
                            <div className="col-12 mb-4">
                                <h1 className="mb-3 text-light">{productName}</h1>
                                <h4 className="bg-light text-dark p-2 d-block">&#36;{price} - Status: <b>{statusString}</b></h4>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        product: state.viewProduct,
        productPrice: state.viewProductPrice,
        messageBanner: state.messageBanner
    };
}
  
export default connect(
    mapStateToProps, 
    { setBannerMessage,
    resetBanner }
)(ProductOverview);