import React from 'react';

function CouponEntry(props) {
    return (
        <div className="text-light">
            <h3 className="mb-3 py-2 d-flex justify-content-center ">Coupon Code</h3>
            <p className="d-flex justify-content-center text-center">For special offers and reduced prices, enter a coupon code below</p>
            <form>
                <div className="form-group d-flex justify-content-center">
                    <input className="form-control" type="text" placeholder="Enter Code Here" />
                </div>
            </form>
        </div>
    );
}

export default CouponEntry;