import actionTypes from './actionTypes';
import CartUtility from '../utilityComponents/Cart';

export const setCartProducts = (cartProducts) => async dispatch => {
    dispatch({
        type: actionTypes.SET_CART_PRODUCTS,
        payload: cartProducts
    });
}

export const addCartProduct = (productBundle, cartProducts) => async dispatch => {
    // add to state
    const newCart = CartUtility.addProductToCart(productBundle,cartProducts);
    // add to local storage
    CartUtility.saveProductsToStorage(newCart);
    dispatch({
        type: actionTypes.ADD_PRODUCT_TO_CART,
        payload: newCart
    });
}

export const removeCartProduct = (productId, cartProducts) => async dispatch => {
    // remove from state
    const newCart = CartUtility.removeProductById(productId,cartProducts);
    // add to local storage
    CartUtility.saveProductsToStorage(newCart);
    dispatch({
        type: actionTypes.REMOVE_PRODUCT_FROM_CART,
        payload: newCart
    });
}

export const updateQuantityById = (productId, itemQuantity, cartProducts) => async dispatch => {
    // pull products from local storage
    const newCart = CartUtility.updateQuantityById(productId, itemQuantity, cartProducts);
    // sync with local storage
    CartUtility.saveProductsToStorage(newCart);
    dispatch({
        type: actionTypes.UPDATE_QUANTITY_BY_ID,
        payload: newCart
    });
}

export const fetchCartProducts = () => async dispatch => {
    // pull products from local storage
    const cartItems = CartUtility.getAllProductsInCart();
    dispatch({
        type: actionTypes.FETCH_CART_PRODUCTS,
        payload: cartItems
    });
}