import React from 'react';

// UI COMPONENTS
import UniversalButton from './UniversalButton.js';

function ProductThumbnail(props) {
    // <a className="info btn btn-light btn-md" role="button" href="#">View Product</a>
    
    const LargeCol = "col-sm-12 col-md-12 col-lg-4 mb-2";
    const SmallCol = "col-sm-12 col-md-6 col-lg-4 mb-2";
    return (
        <div className={props.containerSize === "wide"? LargeCol : SmallCol}>
            <div className="hovereffect text-light">
                <img className="img-responsive imageStyle" src={props.imageLink} alt=""/>
                <div className="overlay">
                    <h4 className="project-category text-faded productCategory">
                        {props.productCategory}
                    </h4>
                   <h2 className="productName">{props.productName}</h2>
                   <h3 className="project-category text-faded productPrice">
                       &#36;{props.price}
                   </h3>
                   <UniversalButton
                        buttonColor={"light"}
                        id={''}
                        buttonLink={props.productLink}
                        buttonText={'View Product'}
                    />
                </div>
            </div>
        </div>
    );
}

export default ProductThumbnail;