import React from 'react';
import { connect } from 'react-redux';
import { resetBanner } from '../../actions/messageBannerActions';

class MessageBanner extends React.Component {
    constructor(props) {
        super(props);
        
        // BINDS
        this.renderTimedBanner = this.renderTimedBanner.bind(this);
    }
    
    // UI DISPLAY METHODS
    renderTimedBanner() {
       // this method takes in a message and alertype
        // parameters get called in setState
        // setState will be set back to null after 5 seconds
        /*this.setState({
            bannerInfo: {
                message: messageString,
                alertType: alertType
            } 
        });
        
        setTimeout(()=>{
            this.setState({
                bannerInfo: null,
            })
        },5000);
        this.setState({
            bannerInfo: {
                message: messageString,
                alertType: alertType
            } 
        });
        
        setTimeout(()=>{
            this.setState({
                bannerInfo: null,
            })
        },5000);*/
        
        setTimeout(()=>{
            /*this.setState({
                bannerInfo: null,
            })*/
            this.props.resetBanner();
        },6500);
    }
    
    render() {
        const reduxMessage = this.props.messageBanner;
        let banner = null;
        if(reduxMessage !== null) {
            const alertType = reduxMessage.alertType;
            const message = reduxMessage.messageText;
            banner = (
                <div className="row">
                    <div className="col-sm-12 col-md-12 col-lg-12">
                        <div className={"alert alert-"+alertType} role="alert">
                          <b>{message}</b>
                        </div>
                    </div>
                </div>
            );
            this.renderTimedBanner();
        }
        return (
            <div>
            { banner }
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        messageBanner: state.messageBanner
    };
}
  
export default connect(
    mapStateToProps, 
    { resetBanner }
)(MessageBanner);