import React from 'react';

// ASSET IMPORTS
import BackgroundImage from '../../assets/img/arBackgroundRef.jpg';
import Logo from '../../assets/img/whiteTransparent.png'

function HomeHeader(props) {
    return (
        <header className="masthead text-center text-white d-flex" style={{backgroundImage: `url(${BackgroundImage})`}}>
            <div className="container" style={{marginTop: "15%"}}>
                <div className="row">
                    <div className="col" style={{textAlign: "center"}}><img src={Logo} style={{width: "40vh"}} /></div>
                </div>
                <div className="row">
                    <div className="col">
                        <h3 style={{fontSize: "2.5vh"}}><br/>Uncompromising. Unwavering. Uninfringed.<br/><br/></h3>
                    </div>
                </div>
            </div>
        </header>
    );
}

export default HomeHeader;