import React from 'react';

function UniversalFunctionalButton(props) {
    // this function will return a button that accepts text and a color preference
    // this is different from a normal universal button, in that this one does not have a path link
    const WhiteButton = () => (
        <button onClick={props.clickEvent} id={props.id} className={"btn btn-light btn-xl sr-button " + props.extraStyles} role="button" data-aos="zoom-in" data-aos-duration="400" data-aos-once="true">{props.buttonText}</button>
    );
    
    const OrangeButton = () => (
        <button onClick={props.clickEvent} id={props.id} className={"btn btn-light btn-xl sr-button text-light bg-primary " + props.extraStyles} role="button" data-aos="zoom-in" data-aos-duration="400" data-aos-once="false">{props.buttonText}</button>
    );
    
    const Output = () => (props.buttonColor === "orange"? <OrangeButton /> : <WhiteButton />);
    
    return(
        <Output />
    );
}

export default UniversalFunctionalButton;