let InventoryAssistant = (()=>{
    return class {
        // PRICE CHECK HELPERS
        static checkAndReturnPriceFromOptions(optionsPrice, parsedGroupObjs) {
            // this method will take in an array of option group objects
            // the searchNames will be run against the inventory list and determine if there is an additional cost
            let runningCounter = 0;
            parsedGroupObjs.forEach((option)=>{
                const inventoryPricing = optionsPrice;
                inventoryPricing.forEach((priceObj)=>{
                    const pricingList = priceObj.pricingList;
                    pricingList.forEach((item)=>{
                        if(item.optionProperty === option.value)
                            runningCounter+= item.optionAddOnPrice;
                    })
                });
            });
            return runningCounter;
        }
        
        static searchAndReturnOutOfStockItems(itemInventory, currentSelectedOption) {
            // this method returns an array of any inventory items that are out of stock
            // iterate through the inventory items
                // if an inventory item stock count is zero (meaing it's out)
                    // iterate through the item's product configuration (red, blue, leather, big/small, ect.)
                    // if a property is equal to the currently selected value
                        // set has selected val to true
                // will add products that have currentSelectedOption to out of stock array
            const inventory = itemInventory;
            const POV = currentSelectedOption;
            let conditionalOutOfStock = new Array();

            // iterate through inventory objects
            inventory.forEach((currentInvObject)=>{
                const currentStock = currentInvObject.itemCount;
                if(currentStock === 0) {
                    const itemConfiguration = currentInvObject.itemProperties;
                    let hasPOVValue = false;
                    itemConfiguration.forEach((currentOption)=> {
                        const currentOptionName = currentOption.option;
                        // check if POV option is in the item configuration
                        if(currentOptionName == POV.option)
                            hasPOVValue = true;
                    });
                    if(hasPOVValue) {
                        conditionalOutOfStock = conditionalOutOfStock.concat(itemConfiguration);
                    }  
                }
            });
            return conditionalOutOfStock;
            /*
                      <------<------ Extended explaination: ------>------>
                      
                 The point of this function is twofold: determine which values are out of stock and, two, allow for the user to dynamically view the stock status of different item configurations depending on what dropdown they click. What do I mean by that last part? Well, imagine that you have two dropdowns for a shirt product: color and size. Assume, for a minute, that all large red shirts are out of stock. So, let's say you click red in the color dropdown. The out of stock list will now include the property large from the other dropdown. However, let's say you decide, all of the sudden, you click the large shirt next. Now, the red shirt will be in the out of stock list and the large shirt, won't. Matter of fact, the code is now analyizing the other dropdowns and figuring out which combinations for that include large shirt are out of stock.
                
                The whole idea is that "the world revolves around the most recently selected product option", so to speak, because it always gives users the complete freedom to change their minds when selecting options and provides a cleaner UI.
            */
        }
        
        static removeOptionFromOFSArr(outOfStockArr, currentSelectedOption) {
            // this method takes in an out of stock array and selected option
            // will return the a new stock array that doesn't contain any item options that have the selected option in it
            const currentPOV = currentSelectedOption;
            let newStockArr = new Array();
            outOfStockArr.forEach((currentOption)=>{
                if(currentOption.optionName !== currentPOV.optionName)
                    newStockArr.push(currentOption);
            })
            return newStockArr;
            
            /*
                Explaination: This cleans up the array that was build by 'searchAndReturnOutOfStockItems' by cleaning out any references to the option that was originally selected by the user. This is done because, again, we do not want the out of stock array to mark the currently selected item as out of stock.
            */
        }
        
        static getAndReturnOptionTitles(options) {
            // this method grabs the title of each option group
            // adds each option name to arr
            // returns arr of option names
            let nameArr = new Array();
            options.forEach((optionObj)=>{
                const optionName = optionObj.optionName;
                nameArr.push(optionName);
            });
            return nameArr;
        }
        
        static getAndReturnAllOptions(options) {
            // takes in a product options array
            // will get all of the sub-option names from the object
            // then concatenate them into ONE array
            // return data array
            let optionsNameArr = new Array();
            options.forEach((optionObj) => {
                const subOptions = optionObj.optionArray;
                optionsNameArr = optionsNameArr.concat(subOptions);
            });
            return optionsNameArr;
        }
        
        static returnAllTotallyOFSConfigurations(itemInventory, optionNames) {
            // this method just checks if any of the products variants (regardless of combination) are out of stock across the board
            // for example, there might be a variety of shirts in stock, but NOT any in red
            // the property name will be set to state if any are found to be out of stock

            // loop through product options
                // create property exists counter
                // create property out of stock counter
                // loop through inventory data 
                    // if current option is the same as current product property, increment exists counter
                    // if current option is in an out of stock item, increment OFS counter
                // at end of loop, if the exists and outOfStock counters are equal, add property to function-level out of stock array
            let outOfStockNames = new Array();
            optionNames.forEach((option) => {
                // item option counters
                let propertyExists = 0;
                let propertyOutOfStock = 0;
                // inventory looping
                itemInventory.forEach((inventoryItem) => {
                    const stockStatus = inventoryItem.itemCount;
                    const currentItemProperties = inventoryItem.itemProperties;
                    // item property looping
                    currentItemProperties.forEach((property)=>{
                        const optionProperty = property.option;
                        if(optionProperty === option) {
                            propertyExists+=1;
                            if(stockStatus === 0)
                                propertyOutOfStock+=1;
                        }
                    })
                });
                if(propertyExists===propertyOutOfStock)
                    outOfStockNames.push(option);
            });
            return outOfStockNames;
        }
    }
})();

export default InventoryAssistant;