import React from 'react';
import { Link } from 'react-router-dom';

function UniversalButton(props) {
    // this function will return a button that accepts text and a color preference
    const WhiteButton = () => (
        <Link id={props.id} className={"UniversalButton btn btn-light btn-xl sr-button " + props.extraStyles} role="button" data-aos="zoom-in" data-aos-duration="400" data-aos-once="true" to={props.buttonLink}>{props.buttonText}</Link>
    );
    
    const OrangeButton = () => (
        <Link id={props.id} className={"btn btn-light btn-xl sr-button text-light bg-primary " + props.extraStyles} role="button" data-aos="zoom-in" data-aos-duration="400" data-aos-once="false" to={props.buttonLink}>{props.buttonText}</Link>
    );
    
    const Output = () => (props.buttonColor === "orange"? <OrangeButton /> : <WhiteButton />);
    
    return(
        <Output />
    );
}

export default UniversalButton;