import React from 'react';
import { connect } from 'react-redux';

class CartOverview extends React.Component {
    constructor(props) {
        super(props);
        
        // BINDS
        this.calcSubTotal = this.calcSubTotal.bind(this);
        this.calcGrandTotal = this.calcGrandTotal.bind(this);
        this.estimateShippingCostClickEvent = this.estimateShippingCostClickEvent.bind(this);
    }
      
    estimateShippingCostClickEvent(e) {
        e.preventDefault();
        console.log('you need to update the shipping price system');
    }
    
    calcSubTotal(cartItems) {
        // this method takes in cart items
        // loops through each item, multiplying it's price by quantity
        // amount gets added to running counter
        // set subtotal state
        let runningPriceCounter = 0;
        cartItems.forEach((item)=>{
            const quantity = item.quantity;
            const unitPrice = item.price;
            
            const totalPrice = quantity * unitPrice;
            runningPriceCounter+=totalPrice;
        })
        
        return Number(runningPriceCounter).toFixed(2);
    }
    
    calcGrandTotal(shippingCost, subTotal) {
        // add shipping cost and sub total 
        // set state
        return Number(shippingCost + subTotal).toFixed(2);
    }
    
    calcShippingTotal() {
        // TODO: get NPM module that can calculate estimated shipping cost
    }
    
    render() {
        const subTotal = this.calcSubTotal([...this.props.cart]);
        const grandTotal = this.calcGrandTotal(this.props.shippingInfo === null ? 0 : this.props.shippingInfo,subTotal);
        
        // set the proper display characters for shipping price based on props input
        const shippingPrice = this.props.shippingInfo === null ? "Estimate Shipping" : ("$" + this.props.shippingInfo);
        
        return (
            <div className="text-light">
                <h3 className="mb-3 py-2 d-flex justify-content-center">Cart Overview</h3>
                <div className="row">
                    <div className="col-6 d-flex justify-content-center">
                        <p>Subtotal</p>
                    </div>
                    <div className="col-6 d-flex justify-content-center">
                        <p>&#36;{subTotal}</p>
                    </div>
                    <div className="col-6 d-flex justify-content-center">
                        <p>Shipping</p>
                    </div>
                    <div className="col-6 d-flex justify-content-center">
                        <p><a href="#" onClick={this.estimateShippingCostClickEvent}>{shippingPrice}</a></p>
                    </div>
                    <div className="col-6 d-flex justify-content-center">
                        <p>Grand Total</p>
                    </div>
                    <div className="col-6 d-flex justify-content-center">
                        <p>&#36;{grandTotal}</p>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        cart: state.cart,
        shippingInfo: state.shippingInfo
    };
}
  
export default connect(
    mapStateToProps, 
    { }
)(CartOverview);