import React from 'react';
import { connect } from 'react-redux';
import { searchProductByString } from '../../actions/productSearchActions';
import { fetchPageContentByCategory } from '../../actions/sectionContentActions';

// UI COMPONENTS
import Divider from '../sharedComps/Divider.js';
import UniversalButton from '../sharedComps/UniversalButton.js';
import Footer from '../sharedComps/Footer.js';
import SearchResultDivider from './SearchResultDivider';
import SearchDivider from './SearchDivider';

class SearchPage extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            currentCategory: this.props.location.pathname.substr(1,this.props.location.pathname.length-1),
        };
        
        this.DEFAULT_IMAGE = "https://dslv9ilpbe7p1.cloudfront.net/auU7e7rqnAiKOkyMkOGYFQ_store_banner_image.png";
    }
    
    componentDidMount() {
        console.log('re-mount search page')
        this.props.fetchPageContentByCategory(this.state.currentCategory);
    }

    render() {
        // preventing premature rendering errors
        let backgroundImage = "";
        if(Object.keys(this.props.sectionData).length !== 0) {
            backgroundImage = this.props.sectionData.sectionImage === (null || undefined) ? this.DEFAULT_IMAGE : this.props.sectionData.sectionImage;
        }
        
        return (
            <div>
                <div data-bs-parallax-bg="true" style={{height: "500px", backgroundImage: "url("+backgroundImage+")", backgroundPosition: "center",backgroundSize: "cover"}}>
                    <header>
                        <div className="jumbotron pulse animated hero-nature carousel-hero" style={{backgroundColor: "transparent",}}>
                            <h1 style={{textTransform: "capitalize"}} className="hero-title d-flex justify-content-lg-start customPadding">Search</h1>
                        </div>
                    </header>
                </div>

                <SearchDivider />

                <SearchResultDivider currentCategory={this.state.currentCategory} />

                <Divider id={"backToTop"} dividerColor={"dark"} customClass={"text-center text-light"}>
                    <h2 className="mb-4">Done Viewing?</h2>
                    <UniversalButton buttonColor={"white"} buttonText={"back to top"} buttonLink={"/"} />
                </Divider>
            
                <Footer />
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        sectionData: state.sectionContent
    };
}
  
export default connect(
    mapStateToProps, 
    { fetchPageContentByCategory }
)(SearchPage);