import React from 'react';

function SponsorsSection(props) {
    return (
        <section id="sponsors" className="scrollSpySection bg-dark text-white">
            <div className="container text-center">
            <h2 className="mb-4">Thanks to Our Sponsors</h2>
                <div className="row">
                    {props.children}
                </div>
            </div>
        </section>
    );
}

export default SponsorsSection;