import React from 'react';
import FuzzySearch from 'fuzzy-search';
import { connect } from 'react-redux';
import { addProductToFilterList, removeProductFromFilterList, resetProductFilterList, fetchBaseFilter } from '../../actions/productFilterActions';
// import FilterConstants from '../../utilityComponents/FilterConstants.js';

// UI COMPONENTS 
import FunctionalButton from './UniversalFunctionalButton';

class ProductFilter extends React.Component{
    constructor(props) {
        super(props);
        this.state={
            currentCategory: this.props.currentPathname,
            filterArr: null // will be an array of filter objects created based on the products in Redux (search and category products)
        }
        
        this.NUMBER_FORM_CONTROL = 'NUMBER_FORM_CONTROL';
        this.CHECK_BOX_FORM_CONTROL = 'CHECK_BOX_FORM_CONTROL';
        this.RESET_BUTTON = 'resetButton';
        this.APPLY_FILTER_BUTTON = 'applyFilterButton';
        
        // BINDS
        this.generateFilter = this.generateFilter.bind(this);
        this.buildGenericOptionFilter = this.buildGenericOptionFilter.bind(this);
        this.buildCategoryFilter = this.buildCategoryFilter.bind(this);
        this.doesOptionTitleExist = this.doesOptionTitleExist.bind(this);
        this.doesOptionValExist = this.doesOptionValExist.bind(this);
        this.addOptionValToCategory = this.addOptionValToCategory.bind(this);
        this.parseDataIntoFilterFormat = this.parseDataIntoFilterFormat.bind(this);
        this.applyFilter = this.applyFilter.bind(this);
        this.resetFilter = this.resetFilter.bind(this);
        this.returnCheckOptionArr = this.returnCheckOptionArr.bind(this);
        this.filterProductsByOptions = this.filterProductsByOptions.bind(this);
        this.doesProductHaveOption = this.doesProductHaveOption.bind(this);
        this.filterProductsByMedianPrice = this.filterProductsByMedianPrice.bind(this);
        this.doesProductFallWithinPrice = this.doesProductFallWithinPrice.bind(this);
        this.returnMinMaxPrice = this.returnMinMaxPrice.bind(this);
        this.isFilterByStockSelected = this.isFilterByStockSelected.bind(this);
        this.isFilterByOnSaleSelected = this.isFilterByOnSaleSelected.bind(this);
        this.isProductPriceLessThan = this.isProductPriceLessThan.bind(this);
        this.isProductPriceMoreThan = this.isProductPriceMoreThan.bind(this);
        this.resetPriceFilters = this.resetPriceFilters.bind(this);
        this.resetCheckBoxes = this.resetCheckBoxes.bind(this);
    }
    
    async componentDidMount() {
        await this.props.fetchBaseFilter();
        await this.props.resetProductFilterList();
        this.generateFilter();
    }
    
    // EVENT HANDLERS
    async applyFilter() {
        // clear out old filter data
        await this.props.resetProductFilterList();
        // get number fields
        const priceFilters = document.getElementsByClassName(this.NUMBER_FORM_CONTROL);
        // get checkbox fields
        const checkBoxFilters = document.getElementsByClassName(this.CHECK_BOX_FORM_CONTROL);
        
        // check if sorting by sale and/or stock is required
        const shouldFilterByStock = this.isFilterByStockSelected(checkBoxFilters);
        const shouldFilterBySale = this.isFilterByOnSaleSelected(checkBoxFilters);
        
        // parse checkboxes
        const checkedOptions = this.returnCheckOptionArr(checkBoxFilters);
        if(checkedOptions.length !== 0)
            this.filterProductsByOptions(checkedOptions);
        
        // parse price inputs
        const minMaxPrice = this.returnMinMaxPrice(priceFilters);
        if(minMaxPrice.min <= minMaxPrice.max) {
           if(minMaxPrice.min !== 0 && minMaxPrice.max !== 0)
            this.filterProductsByMedianPrice(minMaxPrice);
        else if(minMaxPrice.min !== 0 )
            this.filterByMinPrice(minMaxPrice);
        else if(minMaxPrice.max !== 0)
            this.filterByMaxPrice(minMaxPrice);
        } else {
            this.filterByMinPrice(minMaxPrice);
        }
        
        // filter by sale and stock if required
        if(shouldFilterByStock)
            this.filterProductsInStock();
        if(shouldFilterBySale)
            this.filterProductsBySale();
    }
    
    async resetFilter() {
        // clear out old filter data
        await this.props.resetProductFilterList();
        // get number fields
        const priceFilters = document.getElementsByClassName(this.NUMBER_FORM_CONTROL);
        // get checkbox fields
        const checkBoxFilters = document.getElementsByClassName(this.CHECK_BOX_FORM_CONTROL);
        
        // reset UI
        this.resetPriceFilters(priceFilters);
        this.resetCheckBoxes(checkBoxFilters);
    }
    
    // UI HELPERS
    resetPriceFilters(priceInputs) {
        for(let i = 0; i < priceInputs.length; i++) {
            priceInputs[i].value = "";
        }
    }
    
    resetCheckBoxes(checkboxElements) {
        for(let i = 0; i < checkboxElements.length; i++) {
            checkboxElements[i].checked = false;
        }
    }
    
    // PARSERS
    returnCheckOptionArr(checkBoxElements) {
        // this methods remove the In stock and sale options because they are not searchable by string alone
        let parsedVals = new Array();
        for(let i = 0; i <checkBoxElements.length; i++) {
            const currentEl = checkBoxElements[i];
            if(currentEl.checked && currentEl.value.toLowerCase() !== "in stock" && currentEl.value.toLowerCase() !== "sale")
                parsedVals.push(currentEl.value);
        }
        return parsedVals;
    }
    
    returnMinMaxPrice(priceElements) {
        const minMaxObject = {min: 0, max: 0};
        for(let i = 0; i < priceElements.length; i++) {
            const currentEl = priceElements[i];
            const currentElValue = currentEl.value.trim();
            if(currentElValue !== "") {
                if(isNaN(currentElValue) === false) {
                    if(currentEl.id.toLowerCase() === "min") {
                        minMaxObject.min = currentEl.value;
                    } else {
                        minMaxObject.max = currentEl.value;
                    }
                }
            }
        }
        return minMaxObject;
    }
    
    isFilterByStockSelected(checkBoxElements) {
        for(let i = 0; i <checkBoxElements.length; i++) {
            const currentEl = checkBoxElements[i];
            if(currentEl.value.toLowerCase() === "in stock")
                if(currentEl.checked)
                    return true;
        }
        return false;
    }
    
    isFilterByOnSaleSelected(checkBoxElements) {
        for(let i = 0; i <checkBoxElements.length; i++) {
            const currentEl = checkBoxElements[i];
            if(currentEl.value.toLowerCase() === "sale")
                if(currentEl.checked)
                    return true;
        }
        return false;
    }
    
    // GENERAL FILTERING AND HELPER METHODS
    async filterProductsByOptions(checkOptionsArr) {
        // this method adds the product IDs of products that don't have the specified filtering properties
        // loop through products
        let filteredProductIds = new Array();
        this.props.products.forEach((product)=>{
            let optionCounter = 0;
            // scan each product for the properties
            checkOptionsArr.forEach((option)=>{
                // increment counter if product has option
                if(this.doesProductHaveOption(product,option))
                    optionCounter+=1;
            });
            // filter out product if not all options are present
            if(optionCounter !== checkOptionsArr.length)
                filteredProductIds.push(product.id);
        });
        if(filteredProductIds.length !== 0) {
            // reset filter since this function could fire when options are added or removed
            // add product to non-applicable filtered list
            //if(this.props.filterList.length !== 0)
                //await this.props.resetProductFilterList();
            for(const id of filteredProductIds) {
                await this.props.addProductToFilterList(id);
            }
        }
    }
    
    async filterProductsInStock() {
        let filteredProductIds = new Array();
        this.props.products.forEach((product)=>{
            if(!product.inStock)
                filteredProductIds.push(product.id);
        });
        // return filteredProductIds;
        if(filteredProductIds.length !== 0) {
            for(const id of filteredProductIds) {
                await this.props.addProductToFilterList(id);
            }
        }
    }
    
    async filterProductsBySale() {
        let filteredProductIds = new Array();
        this.props.products.forEach((product)=>{
            if(!product.onSale)
                filteredProductIds.push(product.id);
        });
        // return filteredProductIds;
        if(filteredProductIds.length !== 0) {
            for(const id of filteredProductIds) {
                await this.props.addProductToFilterList(id);
            }
        }
    }
    
    doesProductHaveOption(product, option) {
        // this method will take in product data and an option
        // will then perform a fuzzy search on the product
        // returns true if found
        const productToSearch = new Array();
        productToSearch.push(product);
        const searcher = new FuzzySearch(productToSearch, ['name', 'description', 'category','price','description','weight','options.optionName','options.optionArray'], {
          caseSensitive: false,
        });
        const result = searcher.search(option);
        if(result.length !== 0)
            return true;
        return false;
    }
    
    // PRICE FILTERING AND HELPER METHODS
    async filterProductsByMedianPrice(pricingObject) {
        let filteredProductIds = new Array();
        this.props.products.forEach((product)=>{
            if(!this.doesProductFallWithinPrice(product, pricingObject.min, pricingObject.max))
                filteredProductIds.push(product.id);
        });
        // return filteredProductIds;
        if(filteredProductIds.length !== 0) {
            for(const id of filteredProductIds) {
                await this.props.addProductToFilterList(id);
            }
        }
    }
    
    async filterByMinPrice(pricingObject) {
        let filteredProductIds = new Array();
        this.props.products.forEach((product)=>{
            if(this.isProductPriceLessThan(product, pricingObject.min))
                filteredProductIds.push(product.id);
        });
        // return filteredProductIds;
        if(filteredProductIds.length !== 0) {
            for(const id of filteredProductIds) {
                await this.props.addProductToFilterList(id);
            }
        }
    }
    
    async filterByMaxPrice(pricingObject) {
        let filteredProductIds = new Array();
        this.props.products.forEach((product)=>{
            if(this.isProductPriceMoreThan(product, pricingObject.max))
                filteredProductIds.push(product.id);
        });
        // return filteredProductIds;
        if(filteredProductIds.length !== 0) {
            for(const id of filteredProductIds) {
                await this.props.addProductToFilterList(id);
            }
        }
    }
    
    isProductPriceLessThan(product, minPrice) {
        const price = product.price;
        if(price < minPrice)
            return true;
        return false;
    }
    
    isProductPriceMoreThan(product, maxPrice) {
        const price = product.price;
        if(price > maxPrice)
            return true;
        return false;
    }
    
    doesProductFallWithinPrice(product, minPrice, maxPrice) {
        const price = product.price;
        if(price > minPrice && price < maxPrice)
            return true;
        return false;
    }
    
    // FILTER GENERATION METHODS
    generateFilter() {
        // get filter data from product list
        let filterData = new Array();
        if(this.state.currentCategory.toUpperCase() === "SEARCH")
            filterData = filterData.concat(this.buildCategoryFilter());
        filterData = filterData.concat(this.buildGenericOptionFilter());
        
        // parse filter data filter format
        // add base filter options to filter array
        let filterFormatArr = this.props.filterOptions;
        filterFormatArr = filterFormatArr.concat(this.parseDataIntoFilterFormat(filterData));
        this.setState({ filterArr: filterFormatArr });
    }
    
    buildCategoryFilter() {
        const optionTitle = "Product Category";
        let optionArr = new Array();
        optionArr.push({optionName: optionTitle, options:[]});
        this.props.products.forEach((product)=>{
            // add option if it doesn't exist
            const optionVal = product.category;
            if(!this.doesOptionValExist(optionArr,optionTitle,optionVal))
                optionArr = this.addOptionValToCategory(optionArr,optionTitle,optionVal);
        });
        return optionArr;
    }
    
    buildGenericOptionFilter() {
        let optionArr = new Array();
        
        this.props.products.forEach((product)=>{
            const productOpts = product.options;
            productOpts.forEach((productOption)=>{
                // create new filter object if option does not exist
                const optionName = productOption.optionName;
                if(!this.doesOptionTitleExist(optionArr,optionName)) {
                    // build a new filter group
                    const filterStructure = {optionName:"", options:[]};
                    let newFilterGroup = {...filterStructure};
                    newFilterGroup.optionName = optionName;
                    optionArr.push(newFilterGroup);
                }
                // if option value does not exist in current option array
                    // add vals to appropriate filter object  
                const optionVals = productOption.optionArray;
                optionVals.forEach((optionVal)=>{
                    if(!this.doesOptionValExist(optionArr,optionName,optionVal)) {
                        // add to correct option name object
                        optionArr = this.addOptionValToCategory(optionArr,optionName,optionVal);
                    }
                });
            });
        });
        return optionArr;
    }
    
    // helpers
    doesOptionTitleExist(optionArr, optionTitle) {
        // use the following data structure: {optionName:"", options:[]}
        let doesExist = false;
        optionArr.forEach((optionObj)=>{
            if(optionObj.optionName === optionTitle)
                doesExist = true;
        });
        return doesExist;
    }
    
    doesOptionValExist(optionArr, optionTitle, optionVal) {
        // use the following data structure: {optionName:"", options:[]}
        let doesExist = false;
        optionArr.forEach((optionObj)=>{
            // get sub-options value array
            const options = optionObj.options;
            if(optionObj.optionName === optionTitle) {
                options.forEach((option)=>{
                    if(option === optionVal)
                        doesExist = true;
                });
            }
        });
        return doesExist;
    }
    
    addOptionValToCategory(optionArr, optionTitle, optionValToAdd) {
        // will modify and return a filter array
        optionArr.forEach((optionGroup, index)=>{
            if(optionGroup.optionName === optionTitle)
                optionArr[index].options.push(optionValToAdd);
        })
        return optionArr;
    }
    
    parseDataIntoFilterFormat(filterDataArr) {
        let filterObjectArr = new Array();
        
        filterDataArr.forEach((optionGroup)=>{
            const filterDataStruc = { inputGroupName: "", inputType: "number", inputPropertyNames: new Array() };
            const asFilterObj = filterDataStruc;
            asFilterObj.inputGroupName = optionGroup.optionName;
            asFilterObj.inputPropertyNames = optionGroup.options;
            asFilterObj.inputType = "checkbox";
            // add filter obj to arr
            filterObjectArr.push(asFilterObj);
        });
        
        return filterObjectArr;
    }
    
    render() {
        // copy array from state
        let filterObjCopy = null;
        if(this.state.filterArr !== null)
           filterObjCopy = [...this.state.filterArr];
        return (
            <div>
                <div className="row mb-3">
                    <div className="col-lg-12 text-center">
                        <h2 className="section-heading">Product Filter</h2>
                        <hr className="my-4"/>
                    </div>
                </div>
                <div className="formStyling row mb-5 d-flex justify-content-center">
                    { filterObjCopy === null ? "" :
                        filterObjCopy.map((option)=>
                            <fieldset className="col-lg-4 col-md-6 col-sm-12 row mb-5 d-flex justify-content-center">
                                    <legend className="text-center"><b>{option.inputGroupName}</b></legend>
                                    {
                                        option.inputType === "number" ?
                                            
                                        option.inputPropertyNames.map((inputName)=>
                                            <div className="form-group d-flex">
                                                <label className="mr-3" for={inputName}>{inputName}</label>
                                                <input id={inputName} min="1" placeholder="Enter Limit" className={"form-control w-75 " + this.NUMBER_FORM_CONTROL} type={option.inputType} id={inputName}/>
                                            </div>
                                         ) 
                                        :
                                        option.inputPropertyNames.map((inputName)=>
                                            <div className="form-group d-flex p-2">
                                                <div className="form-check">
                                                    <input value={inputName} className={"form-check-input " + this.CHECK_BOX_FORM_CONTROL} type={option.inputType} id={inputName}/>
                                                    <label className="form-check-label" for={inputName}>{inputName}</label>
                                                </div>
                                            </div>
                                         )
                                    }
                            </fieldset>
                        )
                    }
                    <div className="col-6">
                        <button onClick={this.applyFilter} id={this.APPLY_FILTER_BUTTON} className={"btn btn-light btn-xl sr-button text-light bg-primary w-100 mb-3"} role="button" data-aos="zoom-in" data-aos-duration="400" data-aos-once="false">Apply Filter</button>     
                    </div>
                    <div className="col-6">
                        <button onClick={this.resetFilter} id={this.RESET_BUTTON} className={"btn btn-light btn-xl sr-button text-light bg-primary w-100 mb-3"} role="button" data-aos="zoom-in" data-aos-duration="400" data-aos-once="false">Reset</button>  
                    </div>
                </div>
            </div>

        );
    }
}

const mapStateToProps = state => {
    return {
        products: state.searchProducts.searchProducts,
        filterList: state.productFilter.filteredProducts, // this is the list of product Ids that should not be rendered
        filterOptions: state.productFilter.filterObject // this is the physical UI filteration options
    };
}
  
export default connect(
    mapStateToProps, 
    { addProductToFilterList, removeProductFromFilterList, resetProductFilterList, fetchBaseFilter }
)(ProductFilter);