import React from 'react';
import { connect } from 'react-redux';
import { searchProductByString } from '../../actions/productSearchActions';

// UI COMPONENTS
import Divider from '../sharedComps/Divider.js';

class SearchDivider extends React.Component {
    constructor(props) {
        super(props);
        
        this.preSearchMessage = "Begin a search to find your products!"
        
        // BINDS
        this.displaySearchString = this.displaySearchString.bind(this);
        this.searchForProducts = this.searchForProducts.bind(this);
        this.isSearchStringValid = this.isSearchStringValid.bind(this);
    }
    
    isSearchStringValid(stringVal) {
        console.log('logging search string: ')
        console.log(stringVal);
        if(stringVal !== "" && stringVal.length > 1)
            return true;
        else 
            return false;
    }
    
    // DISPLAY METHODS
    displaySearchString() {
        const searchString = this.props.products.searchString;
        console.log('logging search string in display method: ' + searchString)
        // set search string, if one exists
        let displayString = 'Search Results for ';
        if(searchString !== null) {
            // adds the user's search input
            displayString += "'"; 
            displayString += searchString; 
            displayString += "'"; 
        }
        else
            displayString = "Search For Products";
        return displayString;
    }
    
    // SEARCHING METHODS
    searchForProducts() {
        // parse search string
        const searchVal = document.getElementById("searchField").value.trim();
        if(this.isSearchStringValid(searchVal)) {
            // search for products
            this.props.searchProductByString(searchVal);
        }
    }
    
    render() {
        return (
            <Divider id={"searchInput"} dividerColor={"dark"}>
                <div className="row text-light">
                    <div className="col-lg-12 text-center mb-4">
                        <h1 className="section-heading">{this.displaySearchString()}</h1>
                    </div>
                    <div className="col-12 d-flex justify-content-center">
                        <div className="form-group d-flex">
                            <label className="mr-3"><span style={{fontSize: "30px"}} className="fa fa-search"></span></label>
                            <input id="searchField" className="form-control mr-2" placeholder="Enter a product name" type="search"/>
                            <button onClick={this.searchForProducts} className="btn btn-outline-light" style={{width: '250px'}} type="button">Search</button>
                        </div>
                    </div>
                </div>
            </Divider>
        );
    }
}

const mapStateToProps = state => {
    return {
        products: state.searchProducts
    };
}
  
export default connect(
    mapStateToProps,
    { searchProductByString }
)(SearchDivider);