import React from 'react';
import { Nav, NavItem, NavLink, NavBarBrand } from 'reactstrap';
import { Link } from 'react-router-dom';

function NavItemContainer(props) {
    // NOTE: dropdowns DO NOT have BSLinks or JS scroll trigger UI styling
    let dropdownObj = props.dropdownObj;
    let dropdownItems = dropdownObj.dropDownItems; // array of possible dropdown items
    return (
        <NavItem className="dropdown">
            <NavLink tag={Link} className="dropdown-toggle" data-toggle="dropdown" aria-expanded="true" to={dropdownObj.endPoint}>
                {dropdownObj.navText}
            </NavLink>
            <div className="dropdown-menu" role="menu">
                {
                    dropdownItems.map((item)=>
                        <a className="dropdown-item" role="presentation" href={item.endPoint}>{item.navText}</a>
                    )
                }
            </div>
        </NavItem>
    );
}

export default NavItemContainer;