    import React from 'react';
import { connect } from 'react-redux';
import { addCartProduct, removeCartProduct, setCartProducts } from '../../actions/cartActions';
import { applyPriceAddon, resetPriceAddon } from '../../actions/productPriceActions';
import { setBannerMessage } from '../../actions/messageBannerActions';

// IMPORT UI
import OptionDropdown from './OptionDropdown';

// UTILITY CLASS IMPORTS
import CartUtility from '../../utilityComponents/Cart.js';
import InventoryAssistant from '../../utilityComponents/InventoryAssistant.js';

class InputFields extends React.Component {
    // this component will conditionally render the options dropdowns for color and size
    constructor(props) {
        super(props);
        
        this.state = {
            POVStack: new Array(),
            conditionalOutOfStock: null, // based off the product properties that are currently selected
            outOfStockItems: null, // keeps track of the item properties that are out of stock completely (just holds the item property name)
            inputGroupNames: new Array(), // holds a list of strings that are the option group titles
            //bannerInfo: null, // controls whether or not the display message should be rendered
        }
        
        this.OUT_OF_STOCK = "OUT_OF_STOCK";
        
        // BINDS
        this.assignCheckStockToDropdowns = this.assignCheckStockToDropdowns.bind(this);
        this.checkStockAndPrice = this.checkStockAndPrice.bind(this);
        this.updateConditionalItemsState = this.updateConditionalItemsState.bind(this);
        this.initialStoreCheck = this.initialStoreCheck.bind(this);
        this.updateFixedStockItemState = this.updateFixedStockItemState.bind(this);
        this.isOptionPermanentlyOutOfStock = this.isOptionPermanentlyOutOfStock.bind(this);
        this.isOptionConditionalyOutOfStock = this.isOptionConditionalyOutOfStock.bind(this);
        this.updatePOVState = this.updatePOVState.bind(this);
        this.getAndReturnCurrentPOV = this.getAndReturnCurrentPOV.bind(this);
        this.removeItemFromStack = this.removeItemFromStack.bind(this);
        this.addItemToStack = this.addItemToStack.bind(this);
        this.parseAndReturnSelectedDropDownOptions = this.parseAndReturnSelectedDropDownOptions.bind(this);
        this.areDropdownsNotDefault = this.areDropdownsNotDefault.bind(this);
        this.isQuantityLimitValid = this.isQuantityLimitValid.bind(this);
        this.assignAddCartToButton = this.assignAddCartToButton.bind(this);
        this.verifyAndAddItemToCart = this.verifyAndAddItemToCart.bind(this);
        this.areDropdownItemsOFS = this.areDropdownItemsOFS.bind(this);
        this.parseQuantityLimit = this.parseQuantityLimit.bind(this);
    }
    
    // LIFECYCLE METHODS
    componentDidMount() {
        console.log('product io comp has re-mounted')
        console.log(this.props.cart)
        // initial stock check
        this.initialStoreCheck();
        
        // build the array that will be used to house option title values
        const optionGroupTitles = InventoryAssistant.getAndReturnOptionTitles(this.props.product.productData.options);
        this.setState({inputGroupNames: optionGroupTitles});
        
        // assign event handlers to UI elements
        setTimeout(()=>{
            // fixes weird UI timing errors
            this.assignCheckStockToDropdowns(optionGroupTitles);
            this.assignAddCartToButton();
        },0);
    }
    
    // EVENT LISTENER ASSIGNMENTS
    assignCheckStockToDropdowns(elementNamesArr) {
        // this method takes in an array of names that belong to the dropdown elements
        for(let i =0; i < elementNamesArr.length; i++) {
            document.getElementById(elementNamesArr[i]).addEventListener('change',this.checkStockAndPrice);
        }
    }
    
    assignAddCartToButton() {
        // this method wires the event handler add to cart to the add to cart button
        const button = document.getElementById("AddToCartButton");
        button.addEventListener('click',this.verifyAndAddItemToCart);
    }
    
    // EVENT HANDLER METHODS
    checkStockAndPrice(e) {
        // this method takes in a change event from a dropdown and checks the inventory data to determine if the selected options are in stock
        // get event origin
        // if the selected index was not 0 (AKA default)
            // set the select option value to the target event's selectedValue
        // else
            // set the select option val to EMPTY
        // update the state to reflect new option change
        // build an array of search criteria and get the out of stock items
        // if out of stock items length is not zero
            // trim and update stock state based on the length of the out of stock array
        const EMPTY = null;
        const target = e.target;
        const selectedIndex = target.selectedIndex;
        const dropdownId = target.id;
        const selectedValue = target.options[selectedIndex].value;

        if(this.state.inputGroupNames.length >= 2) {
            // operation constants
            const REMOVE = "remove";
            const ADD = "add";

            let optionValue = EMPTY;
            let POVOperation = EMPTY;
            if(selectedIndex != 0 ) {
                // this will set the value that should update the selected options state
                optionValue = selectedValue;
                POVOperation = ADD;
            }
            else {
                // optionval gets marked empty because the user chose the default dropdown value
                optionValue = EMPTY;
                POVOperation = REMOVE;
            }
            // this will get called regardless of the outOfStock return value
            this.updatePOVState(POVOperation, dropdownId, selectedValue);

            // calculate if OFS array should be empty or filled
            let conditionalOutOfStock;
            if(this.getAndReturnCurrentPOV() !== (null || undefined)) {
                const inventory = {...this.props.product.inventoryData}.itemInventory;
                conditionalOutOfStock = InventoryAssistant.searchAndReturnOutOfStockItems(inventory, this.getAndReturnCurrentPOV());            
            } else {
                conditionalOutOfStock = null;
            }

            // apply OFS array value
            // check and add additional pricing
            let stockItems = EMPTY; // always assumed empty by default
            if(conditionalOutOfStock != null && conditionalOutOfStock.length != 0) {
                // trim conditional out of stock list to remove the currently selected option as empty
                stockItems = InventoryAssistant.removeOptionFromOFSArr(conditionalOutOfStock, this.getAndReturnCurrentPOV());
                const parsedDropdownVals = this.parseAndReturnSelectedDropDownOptions();
                const inventoryPricing = {...this.props.product.inventoryData}.optionsPrice;
                const addOnPrice = InventoryAssistant.checkAndReturnPriceFromOptions(inventoryPricing, parsedDropdownVals);
                this.props.applyPriceAddon(addOnPrice);
            } else {
                this.props.resetPriceAddon();   
            }
            this.updateConditionalItemsState(stockItems);
        } 
        else {
            if(selectedIndex === 0) {
                this.props.resetPriceAddon();
            } else {
                const parsedDropdownVals = this.parseAndReturnSelectedDropDownOptions();
                const inventoryPricing = {...this.props.product.inventoryData}.optionsPrice;
                const addOnPrice = InventoryAssistant.checkAndReturnPriceFromOptions(inventoryPricing, parsedDropdownVals);
                this.props.applyPriceAddon(addOnPrice);
            }
        }
    }
    
    verifyAndAddItemToCart(e) {
        e.preventDefault();
        // this method takes in the click event from the button and prevents default
        // will parse the values from the product combo boxes and quantity input
        // if both are valid
            // check if product is already in cart
                // call build product bundle
                // pass bundle into Cart static class method
            // else
                // display bootstrap banner
        // else 
            // display bootstrap banner showing the errors
        const parsedComboBoxOptions = this.parseAndReturnSelectedDropDownOptions();
        const productId = this.props.product.productData.id;
        const warningAlert = "warning";
        const successAlert = "success";
        let alert = "";
        let message = "";
        if(this.isQuantityLimitValid()) {
            if(this.areDropdownsNotDefault(parsedComboBoxOptions) === true) {
                if(this.areDropdownItemsOFS(parsedComboBoxOptions) !== true) {
                    if(CartUtility.getProductById(productId, [...this.props.cart]) === null) {
                        // add to cart
                        // set message to success
                        // call build product bundle
                        // set local storage
                        // set cart state
                        const productQuantity = this.parseQuantityLimit(); 
                        const productBundle = CartUtility.bundleProduct(this.props.product.productData,productQuantity,parsedComboBoxOptions); 
                        this.props.addCartProduct(productBundle, [...this.props.cart]);
                        this.props.resetPriceAddon();
                        message = "Item Added to Cart!";
                        alert = successAlert;
                    }
                    else {
                        // set message to already in cart
                        message = "Item Already In Cart.";
                        alert = warningAlert;
                    }
                }
                else {
                    // set message to cannot be out of stock
                    message = "Cannot Select an Out of Stock Item.";
                    alert = warningAlert;
                }
            }
            else {
                // set message to cannot be default
                message = "All Category Options Must be Selected.";
                alert = warningAlert;
            }
        }
        else {
            message = ("Quantity must be less than: " + this.state.quantityLimit + ".");
            alert = warningAlert;
        }
        this.props.setBannerMessage(message,alert);
    }
    
    // STORE CHECK HELPERS
    initialStoreCheck() {
        // this method just checks if any of the products variants (regardless of combination) are out of stock across the board
        // for example, there might be a variety of shirts in stock, but NOT any in red
        // the property name will be set to state if any are found to be out of stock
        
        const inventory = {...this.props.product.inventoryData}.itemInventory;
        const optionNames = InventoryAssistant.getAndReturnAllOptions([...this.props.product.productData.options]);
        const outOfStockNames = InventoryAssistant.returnAllTotallyOFSConfigurations(inventory,optionNames);
        
        // set state if array is not empty
        if(outOfStockNames.length !== 0)
            this.updateFixedStockItemState(outOfStockNames);
    }
    
    // PARSING AND VALIDATION METHODS
    parseAndReturnSelectedDropDownOptions() {
        // this method will get the dropdown groups by id and return an array of objects
        // objects will have two properties each: groupName and value
        // array will be returned
        let optionValues = [...this.state.inputGroupNames];
        let parsedValArr = new Array();
        optionValues.forEach((optionName)=> {
            const dropdown = document.getElementById(optionName);
            const value = dropdown.options[dropdown.selectedIndex].value;
            
            let resultObj = {};
            resultObj.value = value;
            resultObj.optionName = optionName;
            
            parsedValArr.push(resultObj);
        })
        return parsedValArr;
    }
    
    parseQuantityLimit() {
        // method grabs the quantity number from the input field
        const quantityInput = document.getElementById("quantityInput");
        return quantityInput.value;
    }
    
    isQuantityLimitValid() {
        // check to see if quantity limit is less than or equal to the one specified on the product data
        const quantityInput = document.getElementById("quantityInput");
        if(quantityInput.value <= this.props.product.productData.quantityLimit)
            return true;
        return false;
    }
    
    areDropdownsNotDefault(parsedValues) {
        // this method checks to see if the value of a dropdown is zero
        let isValid = true;
        parsedValues.forEach((result)=>{
            const val = result.value;
            if(val == 0) {
                isValid = false;
            } 
        });
        return isValid;
    }
    
    areDropdownItemsOFS(parsedValues) {
        // this method checks to see if the value of a dropdown is not marked as out of stock
        let isValid = false;
        parsedValues.forEach((result)=>{
            const val = result.value;
            if(this.isOptionConditionalyOutOfStock(val) || this.isOptionPermanentlyOutOfStock(val)) 
                isValid = true;
        });
        return isValid;
    }
    
    // STATE UPDATING AND INITIALIZER METHODS
    updatePOVState(operation, categoryName, optionName) {
        // this method takes in an option category name and an operation ("add" or "remove") and pushes it to the POV state
        // grabs item on top of array and set it to null if POVStack is empty
        const povStack = [...this.state.POVStack];
        const firstItem = povStack.length===0 ? null : povStack.pop();
        
        // check operation
        let newStackItems = null;
        if(operation==="add") {
            // add to stack if the first item is not ALREADY the category
            if(firstItem !== categoryName || firstItem === null) {
               newStackItems= this.addItemToStack([...this.state.POVStack],categoryName, optionName);
            }
        }
        else {
            newStackItems= this.removeItemFromStack([...this.state.POVStack],categoryName);
        }
        // set new stack items to state if not null
        if(newStackItems !== null) {
            this.setState({
                POVStack: newStackItems,
            })
        }
    }
    
    updateConditionalItemsState(value) {
        // this method sets the stockItems state value
        // takes in an array of inventory item objects
        this.setState({
            conditionalOutOfStock: value,
        });
    }
    
    updateFixedStockItemState(value) {
        // takes in an array of option names
        // add them to out of stock state
        this.setState({
            outOfStockItems: value,
        })
    }
    
    // POV HELPERS METHODS
    removeItemFromStack(stackArr, categoryName) {
        // takes in a stack of category name objects and the category to be removed
        // builds a new stack and removes any instances of the category name
        let newStack = new Array();
        for(let i =0; i <stackArr.length; i++) {
            const currentItem = stackArr[i];
            const currentItemCategory = currentItem.optionName;
            if(currentItemCategory !== categoryName)
                newStack.push(currentItem);
        }
        return newStack;
    }
    
    addItemToStack(stackArr, categoryName, optionName) {
        // takes in a stack of category names and the name to be added
        // builds storage object
        // cleans arr of category name
        // then pushes category name to new stack
        let optionObj = {};
        optionObj.option = optionName;
        optionObj.optionName = categoryName;
        let cleanedArr = this.removeItemFromStack(stackArr, categoryName);
        cleanedArr.push(optionObj);
        return cleanedArr;
    }
    
    // GETTERS AND CHECKERS
    getAndReturnCurrentPOV() {
        // this method grabs the most recent POV entry and returns it
        const POVCopy = [...this.state.POVStack];
        return POVCopy.pop();
    }
    
    isOptionPermanentlyOutOfStock(option) {
        // takes in the name of an option name and checks if it exists in outOfStockItems state property
        let returnValue = false;
        if(this.state.outOfStockItems !== null) {
            this.state.outOfStockItems.forEach((item)=>{
                if(item === option)
                    returnValue = true;
            });
        }
        return returnValue;
    }
    
    isOptionConditionalyOutOfStock(option) {
        // takes in the name of an option name and checks if it exists in outOfStockItems state property
        let returnValue = false;
        if(this.state.conditionalOutOfStock !== null) {
            this.state.conditionalOutOfStock.forEach((item)=>{
                if(item.option === option)
                    returnValue = true;
            });
        }
        return returnValue;
    }
    
    render() { 
        let productOptions = null;
        let quantityLimit = null;
        const options = this.props.product.productData.options
        if(options !== null) {
            // generate product options
            // parse product data
            // build stock status UI string
            quantityLimit = this.props.product.productData.quantityLimit;
        }
        return (
            <div className="mb-5">
                <div className="row">
                    <OptionDropdown 
                        options={options}
                        outOfStockItems={this.state.outOfStockItems}
                        conditionalOutOfStock={this.state.conditionalOutOfStock}
                        isOptionPermOFSFunc={this.isOptionPermanentlyOutOfStock}
                        isOptionConditionalOFSFunc={this.isOptionConditionalyOutOfStock}
                    />
                    <div className="col-sm-6 col-md-6 col-lg-4 mb-4">
                        <div className="form-group">
                            <label className="text-light">Quantity (Limit {quantityLimit} Per Customer)</label>
                            <br />
                            <input className="form-control" type="number" min="1" max={quantityLimit} defaultValue="1" id="quantityInput"/>
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="col-sm-12 col-md-6 col-lg-4">
                        <button id={"AddToCartButton"} className={"btn btn-light btn-xl sr-button w-100"} role="button" data-aos="zoom-in" data-aos-duration="400" data-aos-once="true">{"Add to Cart"}</button>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        cart: state.cart,
        messageBanner: state.messageBanner,
        product: state.viewProduct,
    };
}
  
export default connect(
    mapStateToProps, 
    { addCartProduct, 
    removeCartProduct, 
    setCartProducts,
    setBannerMessage,
    applyPriceAddon, 
    resetPriceAddon }
)(InputFields);