import React from 'react';
import { Link } from 'react-router-dom';

function CategoryImage(props) {
    return (
        <Link className="portfolio-box" to={props.endPoint}>
            <img className="img-fluid" src={props.thumbnail}/>
            <div className="portfolio-box-caption">
            <div className="portfolio-box-caption-content">
            <div className="project-category text-faded"><span>{props.category}</span></div>
            <div className="project-name"><span>{props.heading}</span></div></div></div>
        </Link>
    );
}

export default CategoryImage;