import actionTypes from './actionTypes';
import * as Server from '../utilityComponents/Server';

export const searchProductByString = (searchString) => async dispatch => {
    // get product from server
    const products = await Server.getProductsBySearchString(searchString);
    
    dispatch({
        type: actionTypes.FETCH_PRODUCTS_BY_SEARCH_STRING,
        payload: {searchString: searchString, productArr: products}
    });
}

