import React from 'react';
import { Nav, NavItem, NavLink, NavBarBrand } from 'reactstrap';
import { Link } from 'react-router-dom';

function NavItemContainer(props) {
    return (
        <NavItem>
            <NavLink tag={Link} className="BSNavLink js-scroll-trigger" to={props.endPoint}>
                <i className={"fa " + props.icon} style={{fontSize: "1.25rem",marginRight: "0.5rem"}}></i>
                {props.navText}
            </NavLink>
        </NavItem>
    );
}

export default NavItemContainer;