import actionTypes from './actionTypes';

export const setBannerMessage = (messageTxt, alertType) => async dispatch => {
    // extra price above default product price
    dispatch({
        type: actionTypes.SET_BANNER_MESSAGE,
        payload: { messageText: messageTxt, alertType: alertType }
    });
}

export const resetBanner = () => async dispatch => {
    // reset banner message
    dispatch({
        type: actionTypes.RESET_BANNER_MESSAGE,
        payload: null
    });
}