    GetAllProducts() {
        return this.productList;
    }
    
    GetProductById(id) {
        // search productlist for id
        // return product with correct id
        for(let i = 0; i < this.productList.length; i++) {
            const currentProduct = this.productList[i];
            if(currentProduct.id == id)
                return currentProduct;
        }
    }
    
    GetProductsByCategory(category) {
        // search productlist for specified category
        // return product array with correct category properties
        let productArray = new Array();
        for(let i = 0; i < this.productList.length; i++) {
            const currentProduct = this.productList[i];
            if(currentProduct.category.toUpperCase() === category.toUpperCase())
                productArray.push(currentProduct);
        }
        return productArray;
    }
    
    GetFeaturedProducts() {
        let productsList = new Array();
        for(let i = 0; i < this.productList.length; i++) {
            const currentProduct = this.productList[i];
            if(currentProduct.featuredProduct)
                productsList.push(currentProduct);
        }
        return productsList;
    }
    
    SearchFromProducts(searchString) {
        // get all products
        const products = this.productList;
        const searcher = new FuzzySearch(products, ['name', 'description', 'category','price','description','weight','options.optionName','options.optionArray'], {
          caseSensitive: false,
        });
        const result = searcher.search(searchString);
        return result;
    }
